.product-comments{
  margin-bottom: 15px;
  span{
    display: none;
  }
  .star_content{
    display: inline-block;
    vertical-align: middle;
    padding-right: 15px;
    position: relative;
    @include media-breakpoint-down(md) {/* If media is below 992 */
      display: block;
      padding-right: 0;
      margin-bottom: 10px;
      &:after{
        display: none;
      }
    }
    @include media-breakpoint-down(sm) {/* If media is below 768 */
      display: inline-block;
      vertical-align: middle;
      margin-bottom: 0;
      padding-right: 15px;
      &:after{
        display: block;
      }
    }
    @include media-breakpoint-down(xs) {/* If media is below 544 */
      display: block;
      padding-right: 0;
      margin-bottom: 10px;
      &:after{
        display: none;
      }
    }
    &:after{
    	content: '';
    	height: 20px;
    	width: 1px;
    	background: #cccccc;
    	position: absolute;
    	top: 0;
    	right: 0;
    }
    .star{
      color: $brand-primary;
      display: inline-block;
      vertical-align: middle;
      cursor: pointer;
      margin: 0 -2px;
      font-size: 16px;
      position: relative;
      &:after{
        content: "\E838";
        font-family: "Material Icons";
        color: #d9d9d9;
      }
    }
    .star_on{
      &:after{
        content: "\E838";
        font-family: "Material Icons";
        color: $brand-secondary;
      }
    }
  }
  .comment_advice{
  	display: inline-block;
    vertical-align: middle;
    a{
      float: left;
    	text-decoration: none;
    	padding: 0 8px;
    	position: relative;
      font-size: 0.9375rem;
      color: #232323;
      float: left;
      @include media-breakpoint-down(md) {/* If media is below 992 */
        padding-left: 0;
      }
      @include media-breakpoint-down(sm) {/* If media is below 768 */
        padding-left: 8px;
      }
      @include media-breakpoint-down(xs) {/* If media is below 544 */
        padding: 0;
        &:after{
          display: none;
        }
      }
    	&.write_comment{
        @include media-breakpoint-down(md) {/* If media is below 992 */
          padding-left: 15px;
        }
        @include media-breakpoint-down(xs) {/* If media is below 544 */
          margin-bottom: 0;
        }
    		&:after{
    			display: none;
    		}
    	}
    	&:after{
	    	content: '';
	    	height: 20px;
	    	width: 1px;
	    	background: #cccccc;
	    	position: absolute;
	    	top: 0;
	    	right: -4px;
	    }
    	i{
    		font-size: 16px;
  			color: #888888;
  			margin-right: 10px;
  			line-height: 16px;
    	}
    }
  }
}

#reviews{
	#product_comments_block_tab{
		margin: 0;
		.comment{
			padding: 25px 0;
			&:first-child{
				padding-top: 15px;
			}
			.comment_author{
				float: left;
				padding-right: 15px;
				width: 25%;
        @include media-breakpoint-down(xs) {/* If media is below 544 */
          width: 100%;
        }
				span{
					display: none;
				}
				.star_content{
					margin: 0;
					margin-bottom: 15px;
          .star{
            color: $brand-primary;
            display: inline-block;
            vertical-align: middle;
            cursor: pointer;
            margin: 0 -2px;
            font-size: 16px;
            position: relative;
            &:after{
              content: "\E838";
              font-family: "Material Icons";
              color: #d9d9d9;
            }
            a{
              display: none;
            }
          }
          .star_on{
            &:after{
              content: "\E838";
              font-family: "Material Icons";
              color: $brand-secondary;
            }
          }
	      }
      	.comment_author_infos{
      		text-align: left;
      		padding: 0;
      		font-size: 14px;
					font-weight: 500;
					color: $brand-primary;
					.user-comment{
						display: inline-block;
						vertical-align: middle;
						font-weight: 600;
            @include media-breakpoint-down(md) {/* If media is below 992 */
              margin-bottom: 18px;
            }
						i{
							display: none;
						}
					}
					.date-comment{
						display: inline-block;
						vertical-align: middle;
						font-size: 14px;
						margin: 0;
            @include media-breakpoint-down(xs) {/* If media is below 544 */
              vertical-align: top;
              margin-left: 15px;
            }
					}
      	}
			}
			.comment_details{
				float: left;
				width: 75%;
				text-align: left;
				color: $brand-primary;
        @include media-breakpoint-down(xs) {/* If media is below 544 */
          width: 100%;
        }
				.comment_title{
					font-size: 14px;
					padding: 0;
					margin-bottom: 15px;
					font-weight: 600;
					color: $brand-primary;
				}
				.comment_description{
					font-size: 14px;
					color: $brand-primary;
					margin-bottom: 15px;
          @include media-breakpoint-down(xs) {/* If media is below 544 */
            margin-bottom: 10px;
          }
				}
				ul{
					font-size: 16px;
					li{
						float: left;
            @include media-breakpoint-down(md) {/* If media is below 992 */
              float: none; 
              margin-bottom: 15px;
            }
            @include media-breakpoint-down(xs) {/* If media is below 544 */
              line-height: 24px;
              font-size: 15px;
            }
						&.comment_report{
							float: right;
							line-height: 30px;
							text-decoration: none;
              @include media-breakpoint-down(md) {/* If media is below 992 */
                float: none;
                margin-bottom: 0;
              }
						}
						.usefulness_btn{
							background: transparent;
							border: 1px solid #e6e6e6;
							padding: 5px 25px;
							text-transform: uppercase;
							font-size: 15px;
							margin-left: 5px;
              @include media-breakpoint-down(sm) {/* If media is below 768 */
                margin-top: 15px;
              }
              @include media-breakpoint-down(xs) {/* If media is below 544 */
                display: block;
                margin-left: 0;
              }
							&:hover{
								background: $brand-primary;
								border-color: $brand-primary;
							}
							&.yes{
								margin-left: 15px;
								background: #35a26b;
								border-color: #35a26b;
                @include media-breakpoint-down(sm) {/* If media is below 768 */
                  margin-left: 0;
                }
								&:hover{
									background: $brand-primary;
									border-color: $brand-primary;
								}
							}
						}
					}
				}
			}
		}
		.open-comment-form{
			margin: 15px 0 0;
		} 
	}
}


/*========================== Review Model ==========================*/

#new_comment_form{
  .modal-dialog{
    .modal-header{
      text-align: left;
      h4{
        font-size: 16px;
        line-height: 20px;
        color:$brand-primary;
        font-weight: 500;
        display: inline-block;
        vertical-align: middle;
        i{
          display: none;
        }
      }
      .close{
        line-height: 30px;
        opacity: 1;
      }
    }
    .modal-body{
      display: block;
      overflow: hidden;
      .product-image{
        margin-bottom: 15px;
      }
      .product_desc{
        .product_name{
          font-size: 17px;
          font-weight: 400;
          color: $brand-primary;
          margin-bottom: 10px;
          line-height: 24px;
          text-transform: capitalize;
        }
        p{
          font-size: 14px;
          color: #777777;
          font-weight: 400;
        }
      }
      .new_comment_form_content{
        background: #f8f8f8;
        padding: 40px 15px;
        label{
          text-align: left;
          font-size: 14px;
          font-weight: 400;
          float: left;
          margin:0;
          margin-bottom: 5px;
        }
        #criterions_list{
          border-bottom: 1px solid #e6e6e6;
          margin-bottom: 30px;
          label{
            margin: 0 10px 0 0;
          }
          .star_content{
            .star{
              color: $brand-primary;
              display: inline-block;
              vertical-align: middle;
              cursor: pointer;
              margin: 0 -2px;
              font-size: 16px;
              position: relative;
              &:after{
                content: "\E838";
                font-family: "Material Icons";
                color: #d9d9d9;
              }
              a{
                display: none;
              }
            }
            .star_on, .star_hover{
              &:after{
                content: "\E838";
                font-family: "Material Icons";
                color: $brand-secondary;
              }
            }
          }
        }
        .form-group{
          margin-bottom: 30px;
          #content{
            border: 1px solid rgba(0, 0, 0, 0.1);
            padding: 0.5rem 1rem;
          }
        }
        #new_comment_form_footer{
          font-size: 14px;
          margin-top: 0;
          .fl{
            float: none;
            text-align: left;
            margin-bottom: 30px;
          }
          .fr{
            float: none;
          }
        }
      }
    }
  } 
}


