#ishiproductsblock{
	margin-bottom:30px;
	@include media-breakpoint-down(md){
		margin-bottom: 10px;
	}
	.home-title{
		margin-bottom: 25px;
		@include media-breakpoint-down(md) {/* If media is below 992 */
			margin-bottom: 10px;
		}
	}
	.sub-title {
		display: none;
	}
	.ishiproductsblock-container{
		#ishiproductstab{
			border:none;
    		text-align: center;
    		position: relative;
    		margin-bottom: 15px;
    		@include media-breakpoint-down(xs) {/* If media is below 544 */
				margin-bottom: 10px;
			}
			.nav-item {
				@include media-breakpoint-down(xs){/* If media is below 544 */
					display: block;
				}
    			display: inline-block;
    			vertical-align:middle;
    			float: none;
    			margin: 0 10px;
    			&:last-child{
    				@include media-breakpoint-down(xs){/* If media is below 544 */
						margin-bottom: 0px;
					}
    			}
    			.nav-link{
				    @include media-breakpoint-down(xs){/* If media is below 544 */
						background: none;
						padding: 0;
					}
				    background: #f6f6f6 none repeat scroll 0 0;
				    border: medium none;
				    display: inline-block;
				    color: $brand-primary;
				    font-weight:300;
				    font-size: 13px;
				    text-transform: uppercase;
				    margin: 0;
				    outline: medium none;
				    padding: 8px 20px;
				    position: relative;
				    transition: all 400ms ease-in-out 0s;
				    -webkit-transition: all 400ms ease-in-out 0s;
					-moz-transition: all 400ms ease-in-out 0s;
					-o-transition: all 400ms ease-in-out 0s;
					@include media-breakpoint-down(sm){
						padding: 8px 14px;
					}
				    &.active, &:hover{
				    	@include media-breakpoint-down(xs){/* If media is below 544 */
							background: none;
							color: $brand-secondary;
						}
						background: $thired-color;
						color: $brand-primary;
				    }
    			}
    		}	
		}
	}
}


