#smartblog_block{
	@include media-breakpoint-down(md){
		margin-bottom: 10px;
	}
	position: relative;
	margin-bottom: 30px;
	.home-title{
		a{
			color: $brand-secondary;
		}
	}
	.products_block_inner{
		position: relative;
		text-align: center;
		#smartblog-carousel{
			.sds_blog_post{
				padding: 15px 15px;
				margin-bottom: 0 ;	
				&:hover{
					.blog_post{
						.news_module_image_holder{
							position: relative;
							img{
								-webkit-transform-style:unset;
    							transform-style:unset;
								opacity: 0.8;
							}
							.blogicons{
								opacity: 1;	
							}
						}
						.news_module_image_holder {
							.smartbloginfo {
								.blog_date{
									background-color: $thired-color;
									color: $brand-primary;
									&:before{
										border-bottom-color:$thired-color;
									}
								}
							}
						}
						.blog_content{
							.sds_post_title{
								a{
									color: $brand-primary;
								}							
							}
						}
					}
			
				}		
				.blog_post{
					position: relative;
					display: inline-block;
					.news_module_image_holder{
						display: block;
						max-width: 100%;
						position: relative;
						background: $brand-primary;
						img{
							width: 100%;
							transition: all 0.5s;
							-webkit-transition: all 0.5s;
							-moz-transition: all 0.5s;
							-ms-transition: all 0.5s;
							-o-transition: all 0.5s;
						}
						.blogicons{
							display: none;
							left: 0;
							margin: 0 auto;
							position: absolute;
							right: 0;
							text-align: center;
							top: 45%;
							opacity: 0;
							transition: all 0.5s;
							-webkit-transition: all 0.5s;
							-moz-transition: all 0.5s;
							-ms-transition: all 0.5s;
							-o-transition: all 0.5s;
							a{	
								display: inline-block;
								float: none;
								&:hover{
									i{
										&:before{
											color: $light-color;
											background:$brand-secondary;
											border-color: $brand-secondary;
										}
									}
								}
								i{
									&:before{
										border: 1px solid $light-color;
										color: $light-color;
										font-size: 14px;
										height: 40px;
										margin: auto;
										padding: 12px;
										text-align: center;		
										width: 40px;
										line-height: 13px;
										-webkit-transition: all 0.5s;
										-o-transition: all 0.5s;
										transition: all 0.5s;
										-moz-transition: all 0.5s;
										-ms-transition: all 0.5s;
									}
								}
							}
						}
						.smartbloginfo{
							position: absolute;
							top: -8px;
							right: 0;
							color: $brand-primary;
							font-size: 16px;
							font-weight: 300;
							-webkit-transition: all 0.5s;
							-moz-transition: all 0.5s;
							-o-transition: all 0.5s;
							transition: all 0.5s;
							.blog_date{
								font-family: $primary-font;
								display: inline-block;
								padding: 10px 15px;
								background: $brand-secondary;
								color: $light-color;
								-webkit-transition: all 0.5s;
								-moz-transition: all 0.5s;
								-o-transition: all 0.5s;
								transition: all 0.5s;
								i{
									display: none;
									font-size: 16px;
									color: #fff;
									padding-right: 5px;
								}
								.day_date{
									display: block;
									font-size: 25px;
								}
								.day_month{
									font-size: 18px;
								}
								.day_year{
									display: none;
								}
								&:before{
									content: "";
									left: -8px;
									top: 0;
									position: absolute;
									border-bottom: 8px solid $brand-secondary;
									border-left: 8px solid transparent;
									-webkit-transition: all 0.5s;
									-moz-transition: all 0.5s;
									-o-transition: all 0.5s;
									transition: all 0.5s;
								}

							}
							.author{
								display: none;
							}
						}
					}
					.blog_content{
						padding:20px 40px;
						text-align: left;
						background: #f8f8f8;
						.sds_post_title{
							margin-bottom: 10px;
							a{
								font-family: $primary-font;
								font-size: 20px;
								text-transform: uppercase;
								font-weight: 400;
								line-height: 20px;
								color: $brand-secondary;
								-webkit-transition: all 0.4s;
						        -moz-transition: all 0.4s;
						        -o-transition: all 0.4s;
						        transition: all 0.4s;
							}
						}
						p{
							margin-bottom: 10px;
							font-family: $primary-font;
							font-size: 14px;
						}
						.r_more{
							display: none;
						}
					}
					&:hover{
						.smartbloginfo{
							bottom: 0;
						}
					}
				}		
			}
			.owl-nav, .owl-dots{
				display: none;
			}
		}
	}
}


/*<------------------------------------------------------------------------------------>*/
#content-wrapper{
	.block{	
		margin-bottom: 50px;
		text-align: center;
	.articleContent{
		.page-item-title{
			margin-bottom: 30px;
			h1{
				font-size: 22px;
				text-transform: uppercase;
				color: $brand-primary;
				font-weight: 500;
				font-family: $primary-font;
			}
		}
	.post-info{
		margin-bottom: 30px;
		> span{
			font-size: 15px;
			display: inline-block;	
			margin-right: 10px;
			position: relative;
			text-transform: capitalize;	
			color: $brand-primary;
			font-family: $primary-font;
			margin-bottom: 5px;	
			}	
			a{
				font-size: 15px;
				display: inline-block;
				margin-right: 10px;
				position: relative;
				text-transform: capitalize;	
				color: $brand-primary;
				font-family: $primary-font;
			}
			.fa{
				padding-right: 5px;
			} 
			}
			a{		
				img{
					max-width: 100%;
					height: auto;
				}
			}
			.sdsarticle-des{
				padding-top: 30px;
				padding-bottom: 20px;
				font-size: 15px;
				text-align: center;
				line-height: 25px;
				letter-spacing: 0.5px;
				font-family: $primary-font;
				margin: 0 auto;
				width: 100%;
			}
		}
	}
	.smartblogcomments{
		margin-bottom: 50px;
		h4{
			margin-bottom: 15px;
			font-weight: 500;
			color: $brand-primary;
			font-family: $primary-font;
			font-size: 20px;
		}
		.card-block{
			padding:0;
			margin-bottom: 0;
			border:none;
		}
		#commentform{
			margin-top: 30px;
			.form-group {
				margin-bottom: 20px;
				position: relative;
				z-index: 1;
				.form-control-label{
					padding: 0.5rem 15px;
					margin-bottom: 0;
					font-size: 15px;
					font-family: $primary-font;
					text-align: left;
				}
				.form-control{
					&:focus{
						border-color: rgba(0, 0, 0, 0.1);
					}
				}
				.submit{
					#submitComment{
						font: 400 15px/18px $primary-font;
					    padding: 8px 15px;
					    background-color: $thired-color;
					    color: $brand-primary;
					    transition: all 0.5s;
						-webkit-transition: all 0.5s;
						-moz-transition: all 0.5s;
						-ms-transition: all 0.5s;
						-o-transition: all 0.5s;
					    &:hover{
					    	background-color: $brand-secondary;
							color: $light-color;
					    }
					}
					@include media-breakpoint-down(md){
						margin-left: 0;
					}
				}
			}
		}
	}
	#articleComments{
		border-bottom: 1px solid #e5e5e5;
		border-top: 1px solid #e5e5e5;
		margin: 0 0 30px;
		text-align: left;
		.commenttitle{
			font: 400 14px $primary-font;
			margin: 10px 0;
		}
		#comments{
			margin-bottom: 10px;
			.commentList{
				margin: 0;
				position: relative;
				li{
					padding: 8px 0 8px 80px;
					.avatar{
						background: none;
						padding: 0;
						left: 10px;
						position: absolute;
						top: 10px;
					}
					.name {
					    font-size: 13px;
					    font-weight: 700;
					}
					.created {
					    margin: 0 0 10px;
					}
					p {
					    font-size: 15px;
					}
					.reply {
					    height: auto;
					    text-align: right;
					    .comment-reply-link {
						    font: 600 11px/16px $primary-font;
						    padding: 6px 10px;
						    background: #fff;
						    border: 1px solid $brand-primary;
						    color: $brand-primary;
						    &:hover{
						    	background: $brand-primary;
								color: #fff;
								border-color: #brand-primary;
						    }
						}
					}	
				}
			}
		}
	}
}

/*<------------------------------All Blog POSt Page------------------------------------>*/

#content-wrapper{
	#smartblogcat{
		margin-bottom: 30px !important;
		.sdsarticleCat{
			display: block;
			width: 100%;
			overflow: hidden;
			text-align: center;
			&:last-child{
				> div{
					margin-bottom:0;
				}
			}
			> div{
				margin-bottom: 20px;
				font-family: $primary-font;
				font-size: 14px;
				color:#939393;
				font-weight: 400;
				.articleContent{
					margin-bottom: 15px;
				}
				.sdsarticleHeader{
					margin-bottom: 10px;
					p{
						margin-bottom: 0;
						a{
							font-size: 20px;
							text-transform: uppercase;
							color: $brand-primary;
							font-weight: 600;
							text-decoration: none;
						}
					}
				}
				.blogdetail{
					margin-bottom: 10px;
					display: block;
					span{
						font-size: 15px;
						display: inline-block;
						vertical-align: middle;
						margin-right: 10px;
						position: relative;
						text-transform: capitalize;
						color: $brand-primary;
						font-family: $secondary-font;
					}
				}
				.sdsarticle-des{
					display: block;
					margin-bottom: 10px;
				}
				.sdsreadMore{
					a{
						text-decoration: none;
						font-size: 16px;
						font-weight: 500;
					}
				}
			}
		}
	}
	.blog_pagination{
		.pagination{
			margin-top:0;
			margin-bottom: 20px;
			padding: 15px;
			.results{
				@include media-breakpoint-down(xs) {/* If media is below 544 */
					float: none;
					text-align: center;
				}
				float: left;
				line-height: 34px;
			}
			.pagination_bottom{
				@include media-breakpoint-down(xs) {/* If media is below 544 */
					float: none;
					text-align: center;
				}
				margin-bottom: 0;
				display: block;
				float: right;
				li{
					display: inline-block;
					vertical-align: middle;
					a{
						padding: 8px;
						text-align: center;
					}
					&.current, &:hover{
						color:$light-color;
						background:$brand-primary;
					}
				}
			}
		}
	}
}


