.owl-nav{
	text-align: center;
	.owl-prev, .owl-next{
		display: inline-block;
		border: 1px solid $brand-secondary;
    	border-radius: 0;
    	background-color: $brand-secondary;
    	box-shadow: inset 0 0 0px 2px rgba(255,255,255,.5);
    	margin:4px;
    	-webkit-transition: all .5s;
	      -moz-transition: all .5s;
	      -o-transition: all .5s;
	      transition: all .5s;
    	&:hover{
    		border-color: $thired-color;
				box-shadow: inset 0 0 0px 2px $thired-color;
				background-color: $thired-color;
    		i{
    			&:after{
    			color: $brand-primary;
    		}
    	}
    	}
		i{
			line-height: 20px;
			&:after{
				color: #fff;
			    content: "\E314";
			    display: inline-block;
			    font-family: "material icons";
			    font-size: 18px;
			}
		}
	}
	.owl-next{
		i{
			&:after{
				content:"\E315";
			}
		}
	}
}


