#ishibannerblock{
	margin-bottom:50px;
	text-align: center;
	@include media-breakpoint-down(md) {/* If media is below 992 */
		margin-bottom:30px;
	}
	.image-container{
		img{
			max-width: 100%;
			-webkit-transition: all 0.5s;
            -moz-transition: all 0.5s;
            -o-transition: all 0.5s;
            transition: all 0.5s;			
		}
		a{
			display: block;
			position: relative;
			overflow: hidden;
			-webkit-transition: all 0.5s;
		    -moz-transition: all 0.5s;
		    -o-transition: all 0.5s;
		    transition: all 0.5s;
		}	
	}

	.bannerblock{
		display: inline-block;
		vertical-align: middle;
		@include media-breakpoint-down(sm) {/* If media is below 768 */
			margin-bottom: 15px;
			padding: 0;
		}
	}
	.bannerblock2{
		margin-bottom: 30px;
		@include media-breakpoint-down(md) {/* If media is below 768 */
			margin-bottom: 13px;
		}
		@include media-breakpoint-down(sm) {/* If media is below 768 */
			margin-bottom: 15px;
		}
	}
	.bannerblock4{
		margin-bottom: 0;
	}
	.bannerright, .bannercenter, .bannerleft {
		@include media-breakpoint-down(sm) {/* If media is below 768 */
			padding: 0;
		}
	}
}


