/*** Table ***/
.table-bordered {
	border: 1px solid #eaeaea;
	font-size: 13px;
	font-weight: 400;
	letter-spacing: 1px;
	color: #808080;
	line-height: 24px;
	th, td {
	    border: 1px solid #eaeaea;
	}
	thead {
	 	th {
	 		color: #000;
	 		vertical-align: bottom;
			border-bottom: 1px solid #eaeaea;
			font-weight: 600;
			background-color: #f9f9f9;
	 	}
	 	td {
		    border: 1px solid #eaeaea;
		}
	}
	&.table-striped {
		tbody {
			tr{
				&:nth-of-type(2n+1) {
					background-color: transparent;
				}
			}
		}
	}
	td {
		a{
			color: #808080;
			&:hover{
				color:#000000;
				text-decoration: none;
			}
		}
	}
}

.card {
	&.definition-list {
		margin-bottom: 30px;
	}
}


