.block_newsletter {
  @include search-box;
  font-size: 0.875rem;  
  @include media-breakpoint-up(lg) {/* If media is above 991 */
    margin-bottom: 12px;
    padding-left: 50px;
  }
  .newsletter_text{
    text-align: left;
    .block-newsletter-label{
      margin-bottom: 15px;
      margin-top: 0;
    }
  }
  #newsletter {
    padding: 0;
  }
  p {
    margin:5px 0;
  }
  .conditions {
    display: none;
  }
  .alert-danger , .alert-success{
    padding: 6px; 
  }
  .alert-danger{
    color: #a94442 ! important;
  }
  .alert-success{
    color: #3c763d ! important;
  }
 form {
    max-width: 100%;
    position: relative;
    width: 100%;
    margin: 0 auto;
    .input-wrapper {
      overflow: hidden;
    }
    input[type=submit]{
      background-color: $thired-color;
      color:$brand-primary;      
      font-family: $primary-font;
      font-weight: 300;
      font-size: 14px;
      border: none;      
      padding: 8px 15px;
      text-transform: uppercase;
      text-align: center;    
      position: absolute;
      top: 0;
      right: 15px;
      -webkit-transition: all 0.5s;
      -moz-transition: all 0.5s;
      -o-transition: all 0.5s;
      transition: all 0.5s;
      &:hover{
        background-color: $light-color;
        color:$brand-secondary;
      }
    }
      
    input[type=email] {
      min-width: 250px;
      width: 100%;
      height: 40px;
      border: 0;
      border-bottom: 1px solid #3C556D;
      background-color: transparent;
      font-family:$secondary-font;
      font-size:14px;
      font-weight:400;
      padding: 0;
      text-indent: 0px;
      color: #B9B9B9;
      margin-bottom: 20px;
      -webkit-transition: all 0.2s;
      -moz-transition: all 0.2s;
      -o-transition: all 0.2s;
      transition: all 0.2s;
      @include media-breakpoint-down(md) {/* If media is below 992 */
        margin-bottom: 0;
      }
      &:focus, &:hover {
        background-color:transparent;
        border-color: #B9B9B9;
      }     
      &::placeholder {
        color: #B9B9B9;
      }
    }    
  }
}
#block_myaccount_infos{
  width: 19%;
  @include media-breakpoint-down(md) {/* If media is below 768 */
    width:100%;
    display: block;
    overflow: hidden;
  }
}
.block-contact {
  .block{
    margin-bottom: 15px;
    font-family: $secondary-font;
    letter-spacing: 0.5px;
    font-size: 14px;
    font-weight: 400;
    .icon{
      color: $brand-secondary;
      display: block;
      font-family: FontAwesome;
      font-size: 18px;
      height: 20px;
      width: 20px;
    }
    &.address{
      .icon{
        &:before{
          content: "\f041"; 
        }
      }
    }
    &.phone{
      .icon{
        &:before{
          content: "\f095";
        }
      }
    }
    &.fax{
      .icon{
        &:before{
          content: "\f1ac";
        }
      }
    }
    &.email{
      .icon{
        &:before{
          content: "\f0e0";
        }
      }
    }
    .icon{
      content: "";
      height: 22px;
      width: 30px;
      float: left;
      margin: 3px 8px 0 0;
      text-align: center;
      i{
        display: none;
      }
    } 
    .content{
      line-height: 25px;
      overflow: hidden;
    }
  }
}

.social-sharing {
  li{
    a {
      &:before {
        display: inline-block;
        vertical-align: middle;
        font-family: FontAwesome;
        font-weight: 400;
        position: relative;            
        font-size: 18px;
        line-height: 33px;
        color: $light-color;
      }
    }    
    &.facebook{
      background-color:#3b5999;
      a{          
        &:before{
          content: "";
        } 
      } 
    }
    &.twitter{
      background-color: #00aced;
      a{
        &:before{
            content: "\f099";
        }
      }
    }
    &.googleplus{
      background-color: #dc473b;
      a{
         &:before{
          content:"\f0d5";
        }
      } 
    } 
    &.pinterest{
      background-color: #cb2027;
      a{
        &:before{
          content:"\f231";
        }
      }
    }
    &:hover{
      background: $brand-secondary;
    }
  }
}


.block-social { 
  text-align: left;
  display: block;
  overflow: hidden;
  padding-left: 47px;
   @include media-breakpoint-down(md){
    padding-left: 15px;
   }
   @include media-breakpoint-down(xs){
    padding-top: 0px;
    text-align: left;
   }
  p{
    margin-top: 0;
    margin-bottom: 15px;
  }
  ul{
    margin-top: 0;
    margin-bottom:0;
      li{
        cursor: pointer;
        display: inline-block;
        vertical-align: middle;
        border-radius: 50%;
        height: 30px;
        width: 30px;
        padding: 3px;
        margin:0px 3px;
        border-radius: 50%;
        text-align: center;
        position: relative;
        -webkit-transition: all 0.4s;
        -moz-transition: all 0.4s;
        -o-transition: all 0.4s;
        transition: all 0.4s;
        a {
          text-decoration: none;
          &:hover{
            padding:0 ! important;
            color: transparent;
          }
          &:before {
            display: inline-block;
            vertical-align: middle;
            font-family: FontAwesome;
            font-weight: 400;
            position: relative;
            width: 20px;
            font-size: 16px;
            line-height: 24px;
            color: $light-color;
          }
          .socialicon-label {
            display: none;
          }
        }    
        &.facebook{
          a{
             &:before{
              content: "";
            } 
          } 
        }
        &.twitter{
          a{
            &:before{
                content: "\f099";
            }
          }
        }
        &.youtube{
          a{
             &:before{
              content:"\f16a";
            }
          }
        } 
        &.googleplus{
          a{
             &:before{
              content:"\f0d5";
            }
          } 
        } 
        &.rss{
          a{
            &:before{
                content:"\f09e";
            } 
          }
        }
        &.pinterest{
          a{
            &:before{
              content:"\f231";
            }
          }
        }
        &.vimeo{
          a{
            &:before{
              content:"\f27d";
            }
          }
        }
        &.instagram{
          a{
            &:before{
              content:"\f16d";
            }
          }  
        }
        span{
          display: none;
        }
          &:hover{
            border-radius: 6%;
            &.facebook{
              background-color:#3b5999;
            }
            &.twitter{
              background-color: #00aced;
            }
            &.youtube{
              background-color: #d12121;
            }
            &.googleplus{
              background-color: #dc473b;
            }
            &.rss{
              background-color: #f26522;
            }
            &.pinterest{
              background-color: #cb2027;
            }
            &.vimeo{
              background-color: #00aced;
            }
            &.instagram{
              background: -moz-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%); 
              background: -webkit-linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%); 
              background: linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%); 
            }
          }
    }
  }
}


.linkedlist {
  @include media-breakpoint-up(lg) {/* If media is above 991 */
    text-align: center;
    margin:40px 0 20px;
  }
  .linkblock {
    @include media-breakpoint-down(md) {/* If media is below 992 */
      margin-bottom: 15px;
    }
    @include media-breakpoint-up(lg) {/* If media is above 991 */
      ul {
        margin-bottom: 6px; 
        li {
          display: inline;
          margin: 0;
          a {
            line-height: 16px;
          }
          &:after {
            content: "|";
            margin: 0 8px;
          }
          &:last-child {
            &:after {
              content: "";
            }
          }
        }
      }
    }
  }
}

.footer-before {
  .footer-dropdown {
    margin-top:10px;
  }
@include media-breakpoint-down(xs) {/* If media is below 992 */
    .footer-block {
      margin-bottom:15px;
    }
  }
  @include media-breakpoint-up(sm) {/* If media is above 543 */
    .collapse {
        display: inherit;
    }
  }
}

.footer-container {
  overflow: hidden;
  li {
    margin-bottom: 15px;
  }
 
  .footer-dropdown {
    margin-top:10px;
  }
  @include media-breakpoint-up(lg) {/* If media is above 992 */
    .collapse {
        display: inherit;
    }
    .footer-dropdown {
      margin-top:0;
    }
  }
  @include media-breakpoint-down(md) {/* If media is below 992 */
    .footer-block {
      margin-bottom:15px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
.footer-after {
  background-color: #033b64;
  .footer-aftertext {
    text-align: left;
    @include media-breakpoint-down(md) {/* If media is below 992 */
      text-align: center;   
    }
    margin-bottom:0;
    padding:7px 0;
  }
  >.container {
    padding-top: 13px;
    padding-bottom: 13px;
  }
}



