#products,.featured-products,.product-accessories,.product-miniature  {
  padding:15px;
  .ishiproducts-grid & {
    padding: 15px 0;
  }
  .product-thumbnail {
    display: block;
    text-align:center;
  }
  .product-container{
    @include media-breakpoint-down(xs) {/* If media is below 544 */
      max-width: 370px;
      margin: auto;
    }
    .thumbnail-container {
      position: relative;
      overflow: hidden;
      .thumbnail-inner {
        position: relative;
        .product-flags {
          > li {
            position: absolute;
            display: block;
            padding: 5px 7px;
            color: $brand-secondary;
            font-family: $secondary-font;
            text-transform: capitalize;
            font-size: 14px;
            font-weight: 400;
            background-color: rgba(255,255,255,.6);
            -webkit-transition: all 0.3s;
            -moz-transition: all 0.3s;
            -o-transition: all 0.3s;
            transition: all 0.3s;
          }
          .discount, .on-sale{
            top: 5px;
            right: 5px;
            @include media-breakpoint-up(sm){
            opacity: 0;
            transform:translateX(100px);
            -moz-transform:translateX(100px);  
            -webkit-transform:translateX(100px);  
            -o-transform:translateX(100px);  
            -ms-transform:translateX(100px);
          }
          }
          .new {
            top: 5px;
            left: 5px;
            @include media-breakpoint-up(sm){
              -moz-transform:translate(-100px);  
              -webkit-transform:translate(-100px);  
              -o-transform:translate(-100px);  
              -ms-transform:translate(-100px); 
              transform:translate(-100px);
            }
          }
          .online-only {
            left: 5px;
            bottom: 5px;
            display: none;
            &::before{
                content:"\E30A";
                font-family: 'Material Icons';
                vertical-align: -2px;
                margin: 5px;
                font-size: 12px;
            }
          }
          .pack {
            color: $brand-primary;
            right: 5px;
            top: 30px;
            @include media-breakpoint-up(sm){
              transform:translateX(100px);
              -moz-transform:translateX(100px);  
              -webkit-transform:translateX(100px);  
              -o-transform:translateX(100px);  
              -ms-transform:translateX(100px);
            }
          }
        }
        .product-thumbnail {
          display: block;
          text-align: center;
          position: relative;
          img {
            margin: 0 auto;
            max-width:100%;
            -webkit-transition: all 0.7s ease 0s;
            -moz-transition: all 0.7s ease 0s;
            -ms-transition: all 0.7s ease 0s;
            -o-transition: all 0.7s ease 0s;
            transition: all 0.7s ease 0s;
          }
          .product-img-extra, &.change {
            left: 0;
            position: absolute;
            right: 0;
            top: 0;
            opacity: 0;
            -moz-transform: scale(0,1);
            -webkit-transform: scale(0,1);
            -o-transform: scale(0,1);
            -ms-transform: scale(0,1);
            transform: scale(0,1);
          }
          img {
            margin: 0 auto;
            max-width:100%;
          }   
        }
        .thumbnail-buttons {
          color: $brand-secondary;
          position: absolute;
          left: 0;
          right: 0;
          bottom:10px;
          text-align: center;
          cursor: pointer;
          display: inline-block;          
          > div {
            vertical-align: middle;
            font-size: 12px;
            text-transform: uppercase;
            color: $brand-secondary;
            display: inline-block;
          }
          .quickview-btn, .cart-btn, .wishlist-btn{
            @include media-breakpoint-up(sm){
              opacity: 0;
              -moz-transform:translateY(100px);  
              -webkit-transform:translateY(100px);  
              -o-transform:translateY(100px);  
              -ms-transform:translateY(100px);
              -webkit-transition: all .6s;
              -moz-transition: all .6s;
              -o-transition: all .6s;
              transition: all .6s;
            }
            transition-delay: 1s;
            a {
              width: 35px;
              display: inline-block;
              vertical-align: middle;
              height: 35px;
              position: relative;              
              -webkit-transition: all .5s;
              -moz-transition: all .5s;
              -o-transition: all .5s;
              transition: all .5s;
               border: none;
              background: none;
              padding: 0;              
              &:after{
                background:$thired-color url('../img/action.png') no-repeat;
                content: "";
                background-position: center -7px;
                height: 35px;
                width: 35px;
                left: 0;
                right: 0;
                top: -10px;
                display: block;
                margin: 0 auto;
                border-radius: 50%; 
                -webkit-transition: all .5s;
                -moz-transition: all .5s;
                -o-transition: all .5s;
                transition: all .5s;               
              }
              i {
                color: $brand-secondary;
                display: none;
              }
            }
            .quick-view {              
              .lblquickview {
                display: none;
              }
            }
            &:hover{
              a{
                &:after{
                  background-color: $brand-secondary;
                  background-position: center -37px;
                }
              }
            }
          }
          .cart-btn {
            transition-delay: 0.2s;
            a{
              box-shadow: none;              
              &:after{
                background-position: center -68px;
              }
              .lblcart{
                display: none;
              }
            }
            &:hover{
              a{
                &:after{
                  background-color: $brand-secondary;
                  background-position: center -99px;
                }
              }
            }
          }
          .wishlist-btn{
            transition-delay: 0.1s;
            a{              
              &:after{
                background-position: center -132px;
              }
              .lblwishlist{
                display: none;
              }
            }
            &:hover{
              a{
                &:after{
                  background-color: $brand-secondary;
                  background-position: center -163px;
                }
              }
            }
          }
        }
      }
    }
    .highlighted-informations{
        display: none;
    }
    .product-desc {
        display: none;
    }
    &:hover {
      .thumbnail-container {
        .thumbnail-inner {
          .product-thumbnail{
            .change{
              opacity: 1;
              -moz-transform: scale(1,1);
              -webkit-transform: scale(1,1);
              -o-transform: scale(1,1);
              -ms-transform: scale(1,1);
              transform: scale(1,1);
            }
            .product-img-extra{
              #category &{
                opacity: 1;
                -moz-transform: scale(1,1);
                -webkit-transform: scale(1,1);
                -o-transform: scale(1,1);
                -ms-transform: scale(1,1);
                transform: scale(1,1);
              }
            }
          }
          .thumbnail-buttons {
            .quickview-btn, .cart-btn, .wishlist-btn{
              opacity: 1;
              -moz-transform:translateY(0);  
              -webkit-transform:translateY(0);  
              -o-transform:translateY(0);  
              -ms-transform:translateY(0);
            }
          }
          .product-flags{
          	.new, .discount, .pack{
              opacity: 1;
              transform:translateX(0px);
              -moz-transform:translateX(0px);  
              -webkit-transform:translateX(0px);  
              -o-transform:translateX(0px);  
              -ms-transform:translateX(0px);
            }
          }
        }
      }
    }
  }
  .products-section-title {
    text-align: center;
    margin-bottom: rem(24px);
  }

  .product-description {
    margin-top:8px;
    text-align: center;
    .product-title {
      a {
        color: $brand-primary;
        font-size: 15px;
        text-decoration: none;
        text-align: center;
        font-weight: 300;
        text-transform: capitalize;
      }
    }
    
    .product-comments{
      margin-bottom: 5px;
      .star_content{
        display: block;
        padding: 0;
        &:after{
          display: none;
        }
      }
      .comment_advice{
        display: none;
      }
    } 
    .product-price-and-shipping {
      font-family: $secondary-font;
      color: $brand-primary;
      font-size: 15px;
      font-weight: 400;
      span {
        margin-right: 5px;
      }
      .regular-price {
        text-decoration: line-through;
      }
      .discount-percentage, .discount-amount {
        color: $brand-secondary;
        margin: 0 5px;
      }
    }
  }
  .variant-links {
    text-align: left;
  }
  img {
    margin-left: 4px;
  }
  .comments_note {
    text-align: center;
    color: $gray;
  }
  .count {
    color: $gray-dark;
    font-weight: 700;
    position: relative;
    bottom: rem(8px);
  }
  .all-product-link {
    clear: both;
    color: $gray-dark;
    font-weight: 700;
    margin-top: rem(24px);
    margin-bottom: rem(24px);
  }


}

#products {
    /*========================== NO CHANGE ==========================*/
  article {
    &:nth-child(3n+1) {
      clear: left;
    }
    @include media-breakpoint-down(lg) {/* If media is below 1200 */
        &:nth-child(3n+1) {
          clear: none;
        }
        &:nth-child(2n+1) {
          clear: left;
        }
    }
    @include media-breakpoint-down(md) {/* If media is below 992 */
        &:nth-child(2n+1) {
          clear: none;
        }
        &:nth-child(3n+1) {
          clear: left;
        }
    }
    @include media-breakpoint-down(sm) {/* If media is below 768 */
        &:nth-child(3n+1) {
          clear: none;
        }
        &:nth-child(2n+1) {
          clear: left;
        }
    }
    @include media-breakpoint-down(xs) {/* If media is below 544 */
        &:nth-child(2n+1) {
          clear: none;
        }
    }
  }
}

.ishiproducts-grid {
  div {
    &:nth-child(4n+1) {
      clear: left;
    }
    @include media-breakpoint-down(lg) {/* If media is below 1200 */
        &:nth-child(4n+1) {
          clear: none;
        }
        &:nth-child(3n+1) {
          clear: left;
        }
    }
    @include media-breakpoint-down(sm) {/* If media is below 768 */
        &:nth-child(3n+1) {
          clear: none;
        }
        &:nth-child(2n+1) {
          clear: left;
        }
    }

    @include media-breakpoint-down(xs) {/* If media is below 544 */
        &:nth-child(2n+1) {
          clear: none;
        }
    }
  }
}







