#ishistoreinfo{
	width:31%;
	@include media-breakpoint-down(md) {/* If media is below 768 */
		width:100%;
	}
	.ishistoreinfo-inner{
		.store-logo{
			display:block;
			margin-bottom: 14px;
			text-align: left;
			@include media-breakpoint-up(lg) {/* If media is above 991 */
				width: 85%;
			}
			&:hover{
				padding-left:0;
			}
		}
		.block{
			margin-bottom: 15px;
			.icon-text {
			    line-height: 19px;
			}
		}
		.address, .phone, .fax, .email{
			@include media-breakpoint-down(md) {/* If media is below 992 */
				.icon{
					margin-right: 5px;
					i{
						color: $light-color;
					}
				}
			}
			.content{
				display: inline-block;
				line-height: 18px;
				color: #B9B9B9;
				padding-left: 3px;
				a{
					text-decoration: none;
				}
			}
		}
		.store-description{
		    margin-bottom: 15px;
		    p{
				line-height: 24px;
				margin-bottom:0;
				@include media-breakpoint-up(lg) {/* If media is above 991 */
					width: 85%;
				}
		    }
		}
	}
}


