#products {
  color: $gray-dark;
  padding:0;
  .products-select {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
  }
  .up {
    margin-bottom: 1rem;
    .btn-secondary {
      color: $gray;
      text-transform: inherit;
      .material-icons {
        margin-right: 0;
      }
    }
  }
}
.block-category {
  margin-bottom: 1.563rem;
  padding:0;
  border:none;
  #category-description {
    p,
    strong {
      font-weight: 400;
      color: #969696;
    }
    p {
      color: #969696;
      margin-bottom: 0;
      &:first-child {
        margin-bottom: $medium-space;
      }
    }
    span {
      font-size: 14px;
      font-family: $primary-font;
      color: #969696;
    }
  }
  .category-cover {
    img {
      max-width: 100%;
    }
  }
}
.products-selection {
  .sort-by-row {
    display: flex;
    align-items: center;
  }
  .sort-by {
    white-space: normal;
    word-break: break-word;
    margin-right: -0.9375rem;
    margin-left: 0.9375rem;
    text-align: right
  }
  .total-products {
    padding-top: 0.625rem;
    position: relative;
    top: 3px;
    p {
      margin-top: 2px;
    }
  }
  h1 {
    padding-top: 0.625rem;
  }
}
.products-sort-order {
  color: $gray;
  .select-title {
    display: inline-block;
    width: 100%;
    color: $gray-darker;
    background: white;
    padding: $small-space;
    cursor: pointer;
    line-height: 22px;
    border: $input-btn-border-width solid $input-border-color;
  }
  .select-list {
    display: block;
    color: $gray-darker;
    padding: $small-space $medium-space;
    &:hover {
      background: $brand-primary;
      color: white;
      text-decoration: none;
    }
  }
  .dropdown-menu {
    left: auto;
    width: 90%;
    background: $gray-lighter;
    border: none;
    border-radius: 0;
    box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.1);
    margin: 0;
  }
}
.facet-dropdown {
  color: $gray;
  padding-left: 0;
  padding-right: 0;
  background: $gray-light;
  border: 3px solid transparent;
  box-sizing: border-box;
  box-shadow: 1px 1px 1px 1px $gray-light;
  &.open {
    border: 0;
    > .select-title {
      border: 3px solid $brand-primary;
      background: $gray-lighter;
    }
  }
  .select-title {
    display: inline-flex;
    align-items: center;
    margin-left: 0;
    width: 100%;
    color: $gray-darker;
    background: $gray-light;
    padding: $small-space $small-space $small-space $medium-space;
    cursor: pointer;
    > i {
      margin-left: auto;
    }
  }
  .select-list {
    display: block;
    color: $gray-darker;
    background: $gray-lighter;
    padding: $small-space $medium-space;
    &:hover {
      background: $brand-primary;
      color: white;
      text-decoration: none;
    }
  }
  .dropdown-menu {
    padding: 0;
    margin-top: 3px;
    left: auto;
    width: 100%;
    background: $gray-lighter;
    border: none;
    box-shadow: 1px 1px 1px 1px $gray-light;
  }
}
#search_filters,
#search_filters_brands,
#search_filters_suppliers {
  margin-bottom: 25px;
  background: white;
  border: 1px solid #e9e9e9;
  padding: 15px;
  .facet {
    .collapse {
      display: block;
    }
    padding-top: $small-space;
    .facet-title {
      color: #454545;
      font: 500 14px/28px $primary-font;
      text-transform: uppercase;
      margin-bottom: 0;
    }
    .facet-label {
      margin-bottom: 0;
      a {
        font-size: 14px;
        color: #969696;
        display: inline-block;
        line-height: 28px;
        vertical-align: middle;
      }
    }
  }
  .js-search-filters-clear-all {
    text-transform: uppercase;
    background-color: $brand-primary;
    color: $light-color;
    border-color: $brand-primary;
    &:hover {
      background-color:$brand-secondary;
      border-color: $brand-secondary;
    }
  }
}
#search_filters_brands,
#search_filters_suppliers {
  .facet {
    padding-top: 0;
    .facet-label {
      margin-bottom: 0.3125rem;
    }
  }
}
.pagination {
  width: 100%;
  margin-top: 15px;
  margin-bottom:15px;
  padding: 14px 0;
  background-color: #f5f5f5;
  .pagination-desc{
    line-height: 50px;
    font-size:14px;
    color:$black-color;
    @include media-breakpoint-down(sm) {/* If media is below 768 */
    text-align:center;
    }
  }
  .pagination-right{
    float: right;
    text-align: right;
    @include media-breakpoint-down(sm) {/* If media is below 768 */
      text-align:center;
    }
  }
  .page-list {
    padding: rem(10px) 0;
    margin-bottom: 0;
    li {
      display: inline-block;
      vertical-align: middle;
      text-align: center;
    }
    .pagination-lbl {
      display: none;
    }
  }
  a {
    color: black;
    font-weight: 600;
    border:1px solid #eee;
    width: 35px;
    height: 35px;
    padding: 8px;
    text-align: center;
    display: block;
    color: $brand-primary;
    background-color: $light-color;
    box-shadow: 0 0 5px -1px rgba(0,0,0,.1);
    &:hover {
      color:$light-color;
      background-color: $brand-primary;
    }
    &:not(.previous):not(.next) {
      letter-spacing: 0.125rem;
    }
  }
  .previous {
    float: left;
    padding: 5px;
  }
  .next {
    float: right;
    padding: 5px;
  }
  .disabled {
    cursor: not-allowed;
  }
  .current a {
    background-color: $brand-secondary;
    text-decoration: none;
    color: $light-color;
  }
}
.active_filters {
  background: #dededd;
  padding: $small-space $large-space;
  margin-bottom: $medium-space;
  .active-filter-title {
    display: inline-block;
    vertical-align: middle;
    margin-right: 14px;
    font-weight: 500;
    margin-bottom: 0;
  }
  ul {
    display: inline;
    margin: 0;
  }
  .filter-block {
    color: $gray-darker;
    background: white;
    padding: 0.625rem;
    display: inline-block;
    font-size: $font-size-xs;
    .close {
      color: $gray-darker;
      font-size: $font-size-lg;
      opacity: 1;
      margin-top: 1px;
      margin-left: 5px;
    }
  }
}
.block-categories {
  background: #f5f5f5;
  width: 100%;
  .collapse-icons {
    padding:5px;
    i{
      font-size: 16px;
    }
  }
  .category-top-menu {
    margin-bottom: 0;
    padding: 15px;
  }
  .category-sub-menu {
    .category-sub-menu {
      margin-left: 10px;
    }
    .category-sub-link{
      font-size: $font-size-sm;
    }
    li{
      position: relative;
      &[data-depth="1"]{
        margin-bottom: $small-space;
      }
    }
    li[data-depth="0"] > a {
      font-weight: 400;
      width: 100%;
      display: inline-block;
      margin: 0.3125rem 0 0;
      padding-bottom: rem(3px);
      font-size: 14px;
    }
    li:not([data-depth="0"]):not([data-depth="1"]) {
      padding-left: rem(5px);
      &::before {
        content: "-";
        margin-right: rem(5px);
      }
    }
  }
  a {
    color: $gray-darker;
  }
  .collapse-icons {
    position: absolute;
    right: 0;
    top: 0;
    padding: 0;
    cursor: pointer;
    &[aria-expanded="true"] {
      .add {
        display: none;
      }
      .remove {
        display: block;
      }
    }
    .add,
    .remove {
      &:hover {
        color: $brand-primary;
      }
    }
    .remove {
      display: none;
    }
  }
  .arrows {
    .arrow-right,
    .arrow-down {
      font-size: $font-size-sm;
      cursor: pointer;
      margin-left: 2px;
      &:hover {
        color: $brand-primary;
      }
    }
    .arrow-down {
      display: none;
    }
    &[aria-expanded="true"] {
      .arrow-right {
        display: none;
      }
      .arrow-down {
        display: inline-block;
      }
    }
  }
}
.facets-title {
  color: $gray-darker;
}
.products-selection {
  .filter-button {
    .btn-secondary {
      padding: 0.75rem 0.5rem 0.6875rem;
    }
  }
}

/*** Responsive part ***/
@include media-breakpoint-down(md) {
  #category {
    #left-column {
      width: 100%;
      #search_filters_wrapper {
      }
      #search_filter_controls {
        text-align: center;
        button {
          margin: 0 0.5rem;
        }
        margin-bottom: 1rem;
      }
      #search_filters {
        margin-bottom: 15px;
        box-shadow: none;
        padding: 0;
        border-top: 1px solid $gray-lighter;
        .facet {
          .title {
            cursor: pointer;
            .collapse-icons .remove {
              display: none;
            }
          }
          .title[aria-expanded="true"] {
            .collapse-icons {
              .add {
                display: none;
              }
              .remove {
                display: block;
              }
            }
          }
          padding-top: 0;
          border-bottom: 1px solid $gray-lighter;
          .facet-title {
            color: $gray-darker;
            text-transform: uppercase;
          }
          .h6 {
            margin-bottom: 0;
            padding: 0.625rem;
            display: inline-block;
          }
          .navbar-toggler {
            display: inline-block;
          }
          .collapse {
            display: none;
            &.in {
              display: block;
            }
          }
          .facet-label {
            a {
              margin-top: 0;
            }
          }
          ul {
            margin-bottom: 0;
            li {
              border-top: 1px solid $gray-lighter;
              padding: 0.625rem;
            }
          }
        }
      }
    }
    #content-wrapper {
      width: 100%;
    }
    #search_filter_toggler {
      width: 100%;
    }
  }
  .products-sort-order {
    .select-title {
      margin-left: 0;
    }
  }
  .products-selection {
    h1 {
      padding-top: 0;
      text-align: center;
      margin-bottom: 1rem;
    }
    .showing {
      padding-top: 1rem;
    }
  }
  #prices-drop #content-wrapper,
  #new-products #content-wrapper,
  #best-sales #content-wrapper {
    width: 100%;
  }
}

@include media-breakpoint-down(xs) {
  .products-selection {
    .filter-button {
      padding-left: 0;
    }
  }
  #category {
    #left-column {
      #search_filters_wrapper {
        margin-left: -15px;
        margin-right: -15px;
      }
    }
  }
}



