.search-widget { 
  @include search-box;
  cursor: pointer;
  float: left; 
  position: absolute;
  top: 0;
  padding: 22px 15px;
  @include media-breakpoint-down(md) {
    padding: 5px;
    float: right;
    position: relative;
  }
  .search-logo{
    @include media-breakpoint-down(md){
      background:url('../img/sprite.png') no-repeat 10px -30px;
      width:40px;
      height:40px;
      display:block;
    }
  }
  form {
    @include media-breakpoint-up(lg) {
      top: 0;
    }
    @include media-breakpoint-down(md) {/* If media is below 992 */
      display: none;
      z-index: 2;
      position: absolute;
      top: 50px;
      right: 0;
    }
    display: block;
    z-index: 2;
    position: relative;
    top:4px;
    right: 0;
    input[type="text"] {
      min-width: 300px;
      width: 100%;
      border: 1px solid #eaeaea;
      height: 38px;
      padding: 5px 10px;
      background: #ffffff;
      text-transform: capitalize;
      font-size: 14px;
      font-weight: 500;
      letter-spacing: 0.8px;
      color: #a5a5a5;
      &:focus{
        background:#fff;
      }
      &::placeholder{
        color: #a5a5a5;
      }
      @include media-breakpoint-down(xs) {/* If media is below 544 */
        min-width: 240px;
      }
    }
    button[type="submit"] {
      position: absolute;
      color: $brand-secondary;
      background: transparent;
      border: none;
      right: 0rem;
      padding: 0;
      height: 100%;
      width: 48px;
      bottom: 0;
      -webkit-transition: all 0.4s;
      -moz-transition: all 0.4s;
      -o-transition: all 0.4s;
      transition: all 0.4s;
      @include media-breakpoint-down(xs) {/* If media is below 544 */
        width: 32px;
      }
      &:hover{
        color: $light-color;
        background: $brand-secondary;
      }
    }
  }
}
#checkout {
  #search_widget {
    display: none; /* Not ideal solution by allows to reuse same hooks/templates */
  }
}

#pagenotfound {
  .page-content {
    #search_widget {
      width: 100%;
    }
  }
}

.page-not-found {
  .search-widget {
    position: relative;
    padding: 0;
    .search-logo {
        display: none;
    }
    form {
      display: inline-block;
      top: 0;
      position: relative;
    }
  }
}


/**********************************   no change    ************************************/
.ui-widget{
  &.ui-widget-content {
    border-color: #e5e5e5;
    border-radius: 0;
    min-width: 300px;
    padding: 5px;
    max-width: 300px;
    @include media-breakpoint-down(xs) {/* If media is below 544 */
      min-width: 240px;
      max-width: 240px;
    }
    .ui-menu-item{
      a{
        border: medium none;
        border-radius: 0;
        cursor: pointer;
        display: inline-block;
        margin: 0;
        padding: 5px;
        width: 100%;
        &.ui-state-focus{
          background: #e5e5e5;
          margin:0;
        }
        .product-image{
          float: left;
          height: auto;
          margin: 0 15px 0 0;
          max-width: 80px;
        }
        .category{
          color: gray;
          display: block;
          font-size: 14px;
          text-transform: capitalize;
        }
        .product{
          display: block;
          font-size: 14px;
          line-height: 18px;
        }
        .price{
          color: #232323;
          display: block;
          font-size: 14px;
        }
      }
    }
  }
}


