#ishislider {
	margin-bottom: 0px;
	overflow:hidden;
	a{
		vertical-align: middle;
	}
	.owl-nav{
		margin-bottom: 0;
		@include media-breakpoint-down(xs) {/* If media is below 768 */
			display: none;
		}
		.owl-prev, .owl-next{
			border: none;
			border-radius: 0;
			position: absolute;
	    	top: 46%;
			margin:4px;
			left:10px;
			right:auto;
			box-shadow: none;
			padding: 8px !important;
			background-color: $brand-secondary;
			box-shadow: inset 0 0 0px 2px rgba(255,255,255,.5);
			border: 1px solid $brand-secondary;				
			-webkit-transition: all 0.3s;
			-moz-transition: all 0.3s;
			-o-transition: all 0.3s;
			transition: all 0.3s;
			outline: none;
			@include media-breakpoint-up(sm) {/* If media is above 767 */
		      	-moz-transform:translateX(-100px);  
			  	-webkit-transform:translateX(-100px);  
			  	-o-transform:translateX(-100px);  
			  	-ms-transform:translateX(-100px);
			}
			&:hover{
				border-color: $thired-color;
				box-shadow: inset 0 0 0px 2px $thired-color;
				background-color: $thired-color;
				i{
					&:after{
						color:$black-color;
					}
				}
			}
			i {
				&:after {
					@include media-breakpoint-down(xs) {/* If media is below 768 */
						font-size: 24px;
						line-height: 26px;
						margin-left: 0px;
					}
					color: $light-color;
					font-size: 26px;
					line-height: 26px;
					margin-left: -1px;
				}
			}
		}
		.owl-next{
			right: 10px;
			left: auto;
			@include media-breakpoint-up(sm) {/* If media is above 767 */
		      	-moz-transform:translateX(100px);  
			  	-webkit-transform:translateX(100px);  
			  	-o-transform:translateX(100px);  
			  	-ms-transform:translateX(100px); 
			}
		}
	}
	.owl-dots {
		position: absolute;
		bottom: 10px;
		left: 0;
		right: 0;
		margin: 0 auto;
		text-align: center;
	    -webkit-transition: all 0.3s;
		-moz-transition: all 0.3s;
		-o-transition: all 0.3s;
		transition: all 0.3s; 
		.owl-dot {
				outline: none;
				display: inline-block;
				vertical-align: middle;
			span {
				width: 35px;
				height: 3px;
				background: $light-color;
				margin:0 3px;
				display: block;
			}
			&.active {
				span {
					background: $brand-secondary;	
				}
			}
		}
	}
	&:hover {
		.owl-nav{
		.owl-prev, .owl-next{
	    	opacity: 1;
	      	-moz-transform:translate(0);  
		  	-webkit-transform:translate(0);  
		  	-o-transform:translate(0);  
		  	-ms-transform:translate(0);
		}}
	}
}


