#ishitwotwobannerblock{
	margin-bottom:50px;
	text-align:center;
	overflow: hidden;
	@include media-breakpoint-down(md) {/* If media is below 992 */
		margin-bottom: 30px;
	}
	.twobannerblock{
		.image-container{
			img{
				max-width: 100%;
				width: 100%;
				-webkit-transition: all 0.5s;
	            -moz-transition: all 0.5s;
	            -o-transition: all 0.5s;
	            transition: all 0.5s;
			}
			a{
				display: block;
				overflow: hidden;
				position: relative;
				-webkit-transition: all 0.5s;
			    -moz-transition: all 0.5s;
			    -o-transition: all 0.5s;
			    transition: all 0.5s;
			}
		}
	}
	.twobannerblock1 {
		@include media-breakpoint-down(xs) {/* If media is below 544 */
			margin-bottom: 15px;
		}
	}
}


