/*** SOME GENERIC STYLES ***/
@mixin customer-area-base-box {
  @include box-shadow;
}
@mixin customer-area-box {
  @include customer-area-base-box;
  font-size: $font-size-sm;
  color: $gray;
}
.table-labeled {
  th {
    vertical-align: middle;
  }
  td {
    vertical-align: middle;
  }
  .label {
    font-weight: 400;
    border-radius: 3px;
    font-size: inherit;
    padding: 0.25rem 0.375rem;
    margin: 0.125rem;
    color: white;
    white-space: nowrap;
  }
}
.page-order {
  .table {
    margin-bottom: 0;
  }
  table {
    th {
      padding: 0.5rem;
    }
    td {
      padding: 0.5rem;
    }
    thead {
      th {
        text-align: center;
      }
    }
  }
}
#authentication {
  .tooltip.tooltip-bottom {
    padding: 0;
    margin: 0;
  }
  .custom-checkbox {
    display: flex;
    span {
      flex: 0 0 0.9375rem;
    }
    label {
      padding-left: 0.625rem;
    }
  }
}
#identity,
#authentication {
  .radio-inline {
    padding: 0;
    .custom-radio {
      margin-right: 0;
    }
  }
}
/*** Most of the customer account pages ***/
.page-customer-account {
  #content {
    .order-actions {
      a {
        padding: 0 0.125rem;
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;
        &:hover {
          color: $brand-secondary;
        }
      }
    }
    .forgot-password {
      text-align: center;
      font-size: $font-size-sm;
      margin-top: 1rem;
      padding-bottom: 0.9375rem;
    }
    .no-account {
      text-align: center;
      font-size: $base-font-size;
    }
  }
}
/*** Login page ***/
.page-authentication {
  #content {
    @include customer-area-base-box;
    max-width: 640px;
    margin: 0 auto;
  }
}
/*** Addresses page ***/
.page-addresses {
  .address {
    background: #FFFFFF;
    margin-bottom: 1.875rem;
    font-size: 0.875rem;
    color: #808080;
    .address-body {
      padding: 1rem;
      h4 {
        font-size: $base-font-size;
        font-weight: 600;
        color: #000;
      }
      address {
        min-height: 9rem;
        line-height: 1.7;
      }
    }
    .address-footer {
      border-top: 1px solid #ddd;
      padding: 10px 0;
      a {
        color: $brand-primary;
        margin-right: 10px;
        &:hover {
          color: $brand-secondary;
        }
        i {
          font-size: $base-font-size;
        }
        span {
          font-size: $font-size-lg;
          vertical-align: middle;
        }
      }
    }
  }
  .addresses-footer {
    margin: 0 0.9375rem;
    a {
      color: $gray-darker;
      &:hover {
        color: $gray-darker;
      }
      i {
        font-size: $base-font-size;
      }
      span {
        font-size: 15px;
        vertical-align: middle
      }
    }
  }
}
/*** Order details page ***/
.page-order-detail {
  font-size: 0.875rem;
  color: $gray;
  .box {
    border: 1px solid #eaeaea;
    background: $light-color;
    padding: 1rem;
    margin-bottom: 1rem;
  }
  h3 {
    font-size: $base-font-size;
    font-weight: 700;
    text-transform: uppercase;
    color: $gray-darker;
    margin-bottom: 1rem;
  }
  #order-infos {
    ul {
      margin: 0;
    }
    .reorder {
      @include media-breakpoint-down(xs) {/* If media is below 544 */
        padding: 0;
      }
    }
  }
  #order-history {
    .history-lines {
      .history-line {
        padding: 0.5rem 0;
        border-bottom: 1px solid $gray-lighter;
        &:last-child {
          border-bottom: 0;
        }
        .label {
          @include media-breakpoint-down(xs) {/* If media is below 544 */
            font-size: 11px;
          }
          display: inline-block;
          margin: 0.25rem 0;
          padding: 0.25rem 0.375rem;
          color: white;
          border-radius: 3px;
        }
      }
    }
  }
  .addresses {
    margin: 0 -0.9375rem;
    h4 {
      font-size: $base-font-size;
      font-weight: 700;
      color: #232323;
    }
    address {
      line-height: 1.7;
    }
  }
  #order-products {
    &.return {
      margin-bottom: 1rem;
      th {
        &.head-checkbox {
          width: 30px;
        }
      }
      td {
        padding: 1.375rem 0.75rem;
        &.qty {
          min-width: 125px;
          .current {
            width: 30%;
            float: left;
            text-align: right;
            padding-right: 0.5rem;
          }
          .select {
            width: 70%;
            float: left;
            margin: -0.625rem 0;
            padding-left: 0.25rem;
            select {
              text-align: center;
            }
          }
        }
      }
    }
  }
  .order-items {
    padding: 0 !important;
    .order-item {
      padding: 1rem 1rem 0;
      border-bottom: 1px solid $gray-lighter;
      .checkbox {
        width: 30px;
        float: left;
        padding: 0 0.9375rem;
      }
      .content {
        width: calc(100% - 30px);
        float: left;
        padding: 0 0.9375rem;
      }
      .desc {
        .name {
          font-weight: bold;
        }
        margin-bottom: 1rem;
      }
      .qty {
        margin-bottom: 1rem;
        .q {
          margin-bottom: 0.25rem;
        }
        .s {
          margin-bottom: 0.25rem;
        }
      }
    }
  }
  .messages {
    .message {
      margin-top: 0.5rem;
      border-bottom: 1px solid $gray-lighter;
      &:last-child {
        border-bottom: 0;
      }
      > div {
        margin-bottom: 0.5rem;
      }
    }
  }
  .customization {
    margin-top: 0.75rem;
  }
}
/*** Order return page ***/
#order-return-infos {
  .thead-default th {
    color: $gray-darker;
  }
  .customization {
    margin-top: 0.75rem;
  }
}
/*** My account landing page ***/
.page-my-account {
  #content {
    .links {
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      a {
        text-align: center;
        display: inline-block;
        font-size: 16px;
        font-weight: 400;
        text-transform: capitalize;
        color: #000;
        padding: 0 0.9375rem;
        margin-bottom: 1.875rem;
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;
        &#identity-link{
          .link-item{
            &:before{
              content: "\f007";
            }
          }
        }
        &#addresses-link, &#address-link{
          .link-item{
            &:before{
              content: "\f041";
            }
          }
        }
        &#history-link{
          .link-item{
            &:before{
              content: "\f073";
            }
          }
        }
        &#order-slips-link{
          .link-item{
            &:before{
              content: "\f15c";
            }
          }
        }
        &#mywishlist-link{
          .link-item{
            &:before{
              content: "\f004";
            }
          }
        }
        span.link-item {
            border: 1px solid transparent;
            background: #f7f7f7;
            display: block;
            height: 100%;
            padding: 80px 0 20px;
            position: relative;
            &:before{
              color: $brand-primary;
              font-family: FontAwesome;
              font-size: 30px;
              left: 0;
              margin: 0 auto;
              padding-left: 10px;
              padding-right: 10px;
              padding-top: 13px;
              position: absolute;
              right: 0;
              text-align: center;
              top: 20px;
              -webkit-transition: all 0.3s;
              -moz-transition: all 0.3s;
              -o-transition: all 0.3s;
              transition: all 0.3s;
            }
        }
        i {
          display: none;
          font-size: 2.6rem;
          width: 100%;
          color: $gray-darker;
          padding-bottom: 3.4rem;
        }
        &:hover {
          color: #444;
          i {
            color: #444;
          }
          span.link-item {
            &:before {
              color: #444;
            }
          }
        }
      }
    }
  }
}
/*** History page ***/
#history {
  .orders {
    margin: 0 -1rem;
    .order {
      a {
        h3 {
          color: #000;
          font-size: 15px;
          font-weight: 500;
        }
      }
      padding: 0.75rem 1rem;
      border-bottom: 1px solid #eaeaea;
      .label {
        @include media-breakpoint-down(xs) {/* If media is below 544 */
          font-size: 11px;
        }
        display: inline-block;
        margin: 0.25rem 0;
        padding: 0.25rem 0.375rem;
        color: white;
        border-radius: 3px;
      }
      &:last-child {
        border-bottom: 0;
      }
    }
  }
}

/*** FOOTER ***/
.page-footer {
  .account-link {
    margin-right: 1rem;
    i {
      font-size: $base-font-size;
    }
    span {
      font-size: $font-size-sm;
      vertical-align: middle;
    }
  }
}

.login-form {
  margin-top: 15px;
}
.js-address-form, .js-customer-form{
  padding: 20px;
}

#order-slip, #history{
  .page-content{
    padding: 20px;
  }
}




