/*========================== NO CHANGE EXCEPT PADDING AND MARGIN==========================*/

#_desktop_top_menu {
    text-align: center;
    margin: 0 auto;
    display: block;
   #top-menu {
     margin: 0;
   }
}
#top-menu {
  > li {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    text-transform: uppercase;
   
    @include media-breakpoint-down(md) {
      display: block;
      padding: 2px 0;
    }
    .dropdown-item {
        white-space: normal;
    }
    > a {
      padding: 25px 18px;
      font-weight: 400;
      font-size: 16px;
      color: #222;
      text-transform: uppercase;
      transition: all 500ms ease 0s;
      -webkit-transition: all 500ms ease 0s;
      -moz-transition: all 500ms ease 0s;
      -ms-transition: all 500ms ease 0s;
      -o-transition: all 500ms ease 0s;
      &:hover{
        color: $brand-secondary;
      }
    }
    #_desktop_top_menu & {
      .sub-menu {
        &.collapse {
          visibility: hidden;
          transition: all 500ms ease 0s;
          -webkit-transition: all 500ms ease 0s;
          -moz-transition: all 500ms ease 0s;
          -ms-transition: all 500ms ease 0s;
          -o-transition: all 500ms ease 0s;
          transform: translateY(60px);
          -webkit-transform: translateY(60px);
          -ms-transform: translateY(60px);
          -o-transform: translateY(60px);
          -moz-transform: translateY(60px);
          opacity: 0;
        }
        top: 70%;
        left: 0;
        border: none;
        width: auto;
        z-index: 18;
        padding: 15px;
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        -o-transition: all 0.3s;
        transition: all 0.3s;
        text-align: center;
        ul[data-depth="1"] > li {
          float: left;
          width: 200px;
          text-align: left;
          > a{
            color: $brand-secondary;
            font-size: 14px;
            font-weight: 400;
            -webkit-transition: all 0.3s;
            -moz-transition: all 0.3s;
            -o-transition: all 0.3s;
            transition: all 0.3s;
            &:hover {
              color: $brand-secondary;
            }
          }
        }        
        a{
          color: #686868;          
          -webkit-transition: all 0.3s;
          -moz-transition: all 0.3s;
          -o-transition: all 0.3s;
          transition: all 0.3s;
          &:hover {
            color: $brand-secondary;
          }
        }
      }
    }
    &:hover {
      #_desktop_top_menu & { 
        &:hover {
           .sub-menu {
            top: 100%;
            visibility: visible;
            transition: all 500ms ease 0s;
            transform: translateY(0px);
            opacity: 1;
           }
        }
      }
    }
  }   
}
.top-menu {
  a.dropdown-submenu {
    text-transform: uppercase;
    font-weight: 400;
  }
  .collapse {
    display: inherit;
  }
  .category-image {
    max-width: 100%;
  }
  .popover {
    max-width: inherit;
    border-radius: 0;
  }
}
.popover.bs-tether-element-attached-top {
  margin-top: 0;
}

#canvas {
  background-color: rgba(0,0,0,0.7);
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
}

#mobile_top_menu_wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 300px;
  bottom: 0;
  background: white;
  z-index: 999;
  overflow-y:auto;
  overflow-x: auto;
  #top_menu_closer {
    cursor: pointer;
    color:$black-color;
    margin-top: 8px;
    margin-left: 8px;
    text-align: left;
  }
  #_mobile_top_menu {
    padding:10px;
  }
  #top-menu {
    margin-bottom: 0;
    text-align:left;
    display: block;
    padding: 2px 0;
  }
  .top-menu {
    color: $black-color;
    .category-image {
      display: none;
    }
    .collapse-icons[aria-expanded="true"] {
      .add {
        display: none;
      }
      .remove {
        display: block;
      }
    }
    .collapse-icons .remove {
      display: none;
    }
    .navbar-toggler {
      display: inline-block;
      padding: 0;
      position: absolute;
      right: 0;
    }
    a[data-depth="0"] {
      padding: 0;
      margin-bottom: 8px;
    }
    .collapse {
      display: none;
      &.in {
        display: block;
      }
    }
    .sub-menu {
      &.collapse {
        display: none;
        &.in {
          display: block;
        }
      }
      box-shadow: none;
      z-index: inherit;
      display: block;
      position: static;
      overflow: hidden;
      margin-left: 0;
      width: 100%;
      min-width: 100%;
      border:none;
      ul[data-depth="1"] {
        margin: 0;
        padding:0;
        > li {
          float: none;
          margin: 0;
          a {
            text-transform: none;            
          }
        }
      }
      ul {
        padding: 0 0 0 15px;
      }
      li > a {
        font-weight: 500;
      }
      ul[data-depth="2"] li a {
        padding-left: rem(20px);
      }
      ul[data-depth="3"] li a {
        padding-left: rem(40px);
      }
      ul[data-depth="4"] li a {
        padding-left: rem(60px);
      }
    }
    .popover {
      border-radius: 0;
    }
  }
}
#menu-icon {
  color: $light-color;
  padding: 14px 15px;
  font-size: 21px;
}



