#ishimanufacturerblock {
	margin-bottom:10px;
	@include media-breakpoint-down(md) {/* If media is below 992 */
		margin-bottom: 0px;
	}
	.home-title {
		display: block;
	}
	.owl-dots{
		display: none;
	}
	#manufacturer-carousel {
		.owl-item {
			.item {
				.image-container {
					text-align: center;
					img {
						opacity: 0.8;
						max-width: 140px;
						display: inline-block;
						padding: 5px;
						-moz-transform: scale(1);
		                -webkit-transform: scale(1);
		                -o-transform: scale(1);
		                -ms-transform: scale(1);
		                transform: scale(1);
		                transition: all 0.5s;
			            -webkit-transition: all 0.5s;
			            -moz-transition: all 0.5s;
			            -o-transition: all 0.5s;
					}
					&:hover {
						img {
							opacity: 1;
							-moz-transform: scale(1.1);
			                -webkit-transform: scale(1.1);
			                -o-transform: scale(1.1);
			                -ms-transform: scale(1.1);
			                transform: scale(1.1);
						}
					}
				}
			}
		}
		.owl-nav{
			display: none;
		    position: absolute;
		    top: 35%;
		    left: 0;
		    right: 0;
		    margin: 0;
		    z-index: 1;
		    > div {
		    	position:absolute;
		    }
			.owl-prev{
				right: auto;
			    left: -35px;
			}
			.owl-next {
			    right: -35px;
			    left:auto;
			}
		}		
	}
}


