/*========================== NO CHANGE ==========================*/
#block-reassurance{
  margin-top: 15px;
  margin-bottom: 30px;
  img{
    opacity: .9;
    width: rem(25px);
    margin-right: rem(10px);
  }
  li{
    border: 1px 1px 1px solid #eee;
    margin-bottom: 7px;
    &:last-child {
      margin-bottom: 0;
    }
    .block-reassurance-item{
      padding: 15px;
      border-left: 3px solid $brand-primary ! important;
      border:1px solid #e9e9e9;
      background: #f7f8fa;
      text-transform: capitalize;
    }
  }
  span{
    font-weight: 400;
    font-size: 14px;
  }
}



