#ishitestimonials {
  padding: 10px 0;
  background-color: rgb(245, 245, 245);
  margin-bottom: 50px;
  @include media-breakpoint-down(md){
    margin-bottom: 30px;
  }
  color:$brand-primary;
  .home-title{
    display: block;
    padding: 0 0 20px;
    margin-bottom: 25px;
  }
  .parallax {
    padding-top: 40px;
    padding-bottom: 50px;
    background-image: none !important;
    @include media-breakpoint-down(md) {/* If media is below 992 */
      padding-top: 20px;
      padding-bottom: 0px;
  }
  }
  .ishitestimonials-container {
    text-align:center;
    .testimonial-img {
      display: none;
      max-width: 110px;
      border-radius: 50%;
      overflow: hidden;
      margin-right: 50px;
      img{
        max-width: 100%;
      }
    } 
    .user-info {
      display: inline-block;
      vertical-align: middle;
      font-family: $secondary-font;
      width: 75%;
      text-align: center;
      margin: 0 auto;
      margin-bottom: 30px;
      @include media-breakpoint-down(sm) {/* If media is below 768 */
        margin-bottom: 20px;
      }      
      .user-description {
        padding: 0px 20px 20px;
        position: relative;
        line-height: 33px;
        font-weight: 400;
        font-size: 15px;
        letter-spacing: 0.9px;
        color: #333333;
        &:before, &:after{
          content: "\f10d";
          font-family: "FontAwesome";
          color: #d6d6d6;
          font-size: 28px;
          position: relative;
          left: -12px;
        }
        &:after{
          content: "\f10e";
          right: -12px;
          left: auto;
        }
      }
    }

    .user-information{
      text-align: center;
      padding: 0 20px;
      display: block;
      .user-name {
        font-family: $primary-font;
        position: relative;
        padding: 0 20px;
        display: block;
        font-size: 20px;
        font-weight: 600;
        color: $brand-secondary;
        @include media-breakpoint-down(xs) {/* If media is below 544 */
          padding:0 10px;
        }
      }
      .user-designation{
        margin-top: 15px;
        font-weight: 300;
        display: block;
        color: #333333;
      }
    }
  }
  .owl-nav{
    display: none;
    .owl-prev, .owl-next{
      text-align: center;
      border: none;
      background-color: transparent;
      font-size: 0;
      position: absolute;
      left: 0;
      top: 20%;
      display: inline-block;
      -webkit-transition: all ease .3s;
      -moz-transition: all ease .3s;
      transition: all ease .3s;  
      @include media-breakpoint-down(md) {/* If media is below 992 */
        top: 30%;
      }   
      &:after{
          color: $light-color;
          content: "\f177";
          display: inline-block;
          font-family: "FontAwesome";
          font-size: 30px;
          position: absolute;
          top: 0;
          right: 0;
          line-height: 28px;
      }  
      &:hover{
        &:after{
          color: $brand-secondary;
        }
      }   
    }
    .owl-next{
      right: 0px;
      left: auto;
      &:after{
        content:"\f178";
      }
    }
  }
   .owl-dots {
    text-align: center;
    opacity: 1;
    -webkit-transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;
    -ms-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    &:before, &:after{
      display: none;
    }
    .owl-dot {
      display: inline-block;
      margin: 15px 4px 0;
      outline: none;
      width: 143px;
      height: 140px;
      line-height: 136px;
      -ms-transform: scale(.7,.7);
      -webkit-transform: scale(.7,.7);
      transform: scale(.7,.7);
      transition: all 0.5s ease 0s;
      -webkit-transition: all 0.8s ease 0s;
      -moz-transition: all 0.8s ease 0s;
      -ms-transition: all 0.8s ease 0s;
      -o-transition: all 0.8s ease 0s;
      transition: all 0.8s ease 0s;
      @include media-breakpoint-down(xs) {/* If media is below 544 */
        margin: 0;
        width: 95px;
        height: auto;
        -ms-transform: scale(.5,.5);
        -webkit-transform: scale(.5,.5);
        transform: scale(.5,.5);   
        }
      img{
        border-radius: 50%;
        max-width: 100%;
        width: 114px;
        height: 114px;
        border-radius: 50%;
        border: 0 solid $brand-secondary;
        -webkit-transition: all 0.3s ease 0s;
        -moz-transition: all 0.3s ease 0s;
        -ms-transition: all 0.3s ease 0s;
        -o-transition: all 0.3s ease 0s;
        transition: all 0.3s ease 0s;
        opacity: 0.4;
        @include media-breakpoint-down(xs) {/* If media is below 544 */
          height: 98px; 
        }
      }
      span {
        display: block;
        width: 12px;
        height: 12px;
        border:1px solid $brand-primary;
        cursor: pointer;
        border-radius: 50%;
        background:$brand-primary;
      }
      &.active, &:hover {
        span  {
          background: $gray-lighter;
        }
      }
      &.active {
        background: url(../img/round.png) no-repeat center center;
        -ms-transform: scale(1,1);
        -webkit-transform: scale(1,1);
        transform: scale(1,1);
        @include media-breakpoint-down(xs) {/* If media is below 544 */
            background-image: none;
            -ms-transform: scale(.7,.7);
            -webkit-transform: scale(.7,.7);
            transform: scale(.7,.7);
        }
        img{
          width: 120px;
          height: 120px;
          -ms-transform: scale(1,1);
          -webkit-transform: scale(1,1);
          transform: scale(1,1);
          opacity: 1;
          transition: all 0.5s ease 0s;
          @include media-breakpoint-down(xs) {/* If media is below 544 */              
              width: 114px;
              height: 98px;
          }     
        }
        span  {
          cursor: default;
        }
      }     
    }
  }
}


