/*------------Mywishlist page-----------*/

#module-ishiblockwishlist-mywishlist{
	#main{
		> .page-content{
			border: none;
			margin-bottom: 1.5625rem;
			padding: 0px;
		}
	}
	#mywishlist{
		.page-content{
			padding: 1.25rem;
			margin-bottom: 10px;
		}
		.page-subtitle{
			color: #222222;
			font-size: 18px;
			font-weight: 600;
			line-height: 24px;
			margin: 0 auto 15px;
			text-transform: capitalize;
		}
		.form-control{
			display: block;
			color: #777777;
			margin-bottom: 1rem;
			&::placeholder{
				color: #999;
			}
			&:focus{
				border-color: #cfcfcf;
				color: #777777;
			}
		}
		.mywishlist-btn{
			display: block;
			width:100%;
			overflow:hidden;
			.submitWishlist{
				display: block;
				opacity: 1;
				z-index: 2;
				border-radius: 0;	
				line-height: 1;
				margin-bottom: 1rem;		  
			}
		}
		.block-center{
			display: block;
			width: 100%;
			min-height: .01%;
			overflow-x: auto;
			.table-bordered{
				font-size: 14px;
				thead{
					th{
						border-bottom: 2px solid #e9e9e9
					}
				} 
				p{
					color: #777777;
					margin-bottom: 0px;
				}
			}
			.wishlist_delete{
				span{
					display: none;
				}
			}
		}
		/*---------------------------wishlistproduct-----------*/
		#block-order-detail{
			label{
				color: #414141;
				font-weight: 600;
				line-height: 22px;
			}
			.wishlisturl{
				.form-control{
					margin: 0;
					background-color: #e9e9e9;
					border: 1px solid #eeeeee;
					&:focus{
						border-color: #cfcfcf;
						background:$light-color;
					}
				}
			}
			#showSendWishlist{
				display: inline-block;
				vertical-align: middle;
				margin: 0 0 10px;
				a{
					display: block;
				}
			}
			/*---------------------------productanddetail-----------*/
			.wlp_bought{
				.product-container{
					position: relative;
				}
				.lnkdel{
					position: absolute;
					top: 2%;
					right: 5%;
					z-index: 1;
					i{
						color: #666666;
						&:hover{
							color:$brand-secondary;
						}
					}
				}
				.btn_action{
					@include media-breakpoint-down(xs) {/* If media is below 544 */
						text-align: center;
					}
				}
			}
			.hidden{
				display: none;
			}
		}
	}
}

/*---------------------------productpage wishlst-----------*/
	.wishlist_container{
		display: block;
		width: 100%;
		overflow: hidden;
		#wishlist_button{
			select, .product_wishlist_btn{
				float: left;
				width: auto;
			}
			.product_wishlist_btn{
				padding: 11px 10px 11.5px 30px;
				cursor: pointer;
				span{
					padding-left: 15px;
					position: relative;
					&:before{
						color: #666;
						content: "\E87D";
						position: absolute;
						left: -5px;
						font-family: "Material Icons";
					}	
				}
			}	
		}
	}
	.wishlist_login{
		.wishlist_button{
			position: relative;
			cursor: pointer;
			&:before{
				color: #666;
				content: "\E87D";
				position: absolute;
				left: 0;
				top: -1px;
				font-family: "Material Icons";
			}
			span{
				padding-left: 20px;
			}
		}
	}



/*---------------------------wishlst modal-----------*/
#WishlistModal{
	.modal-header{
		.close{
			opacity: 1;
		}
	}
	.modal-body{
		a{
			float: right;
			color: $brand-secondary;
			font-weight: 600;
			text-decoration: underline;
		}
	}
}


