#product {
  #content {
    position: relative;
    margin: 0 auto;
    padding: 0;
    border: 0;
  }
  .product-title {
    color:$black-color;
    font-weight: 400;
    text-transform: capitalize;
    padding-bottom: 15px;
    font-size: 22px;
    border-bottom: 1px solid #ccc;
    margin-bottom: 15px;
  }
}
.form-control-select {
  -moz-appearance: menulist;
  background: none;
}
.product-price { 
  color: $brand-primary;
  display: inline-block;
  vertical-align: middle;
  font-weight: 500;
  font-size: 24px;
}
#product-description-short {
  color: $gray-darker;
}
.product-information {
  font-size: $font-size-lg;
  color: $gray-darker;
  p{
    margin-bottom: 15px;
  }
  .manufacturer-logo {
    height: 35px;
  }
  .product-comments {
    margin-bottom: 20px;
  }

  .product-description img {
    max-width: 100%;
    height: auto;
  }
  .wishlist_login{
    clear: both;
    margin-bottom: 5px;
  }
}
.input-color {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 1.25rem;
  width: 1.25rem;
}
.input-container {
  position: relative;
}
.input-radio {
  position: absolute;
  top: 0;
  cursor: pointer;
  opacity: 0;
  width: 100%;
  height: 100%;
}
.input-color,
.input-radio {
  &:checked + span,
  &:hover + span {
    border: 2px solid $gray-darker;
  }
}
.radio-label {
  @include box-shadow;
  background: white;
  display: inline-block;
  padding: 0.125rem $small-space;
  font-weight: 600;
  border: 2px solid white;
}
.product-actions {
  .control-label {
    width: 100%;
    margin-bottom: 8px;
    display: block;
    width: 100%;
    font-weight: 500;
  }
  .add-to-cart {
    @include media-breakpoint-down(xs) {/* If media is below 544 */
      padding: 10px 19px;
    }
    height: 2.75rem;
    line-height: inherit;
    text-transform: uppercase;
    line-height: 23px;
    padding: 10px 25px;
    background-color: $thired-color; 
    border-color: $thired-color;
    color: $brand-primary; 
    .material-icons {
      line-height: inherit;
      display: none;
    }
    &:hover {
    background-color: $brand-secondary;
    border-color: $brand-secondary;
    color: $light-color;
    }
  }
}
.product-quantity {
  display: block;
  .qty, .add {
    float: left;
    display: inline-flex;
    margin-bottom: 0.5rem;
  }
  .qty {
    margin-right: 15px;
  }
  #quantity_wanted {
    color: $gray-darker;
    background-color: white;
    height: 2.75rem;
    padding: 0.175rem 0.5rem;
    width: 3rem;
  }
  .input-group-btn-vertical {
    float: left;
  }
  .input-group-btn-vertical {
    .btn {
      padding: 0.5rem 0.6875rem;
      i {
        font-size: 1rem;
        top: 0.125rem;
        left: 0.1875rem;
      }
    }
  }
  .btn-touchspin {
    height: 1.438rem;
  }
}
.product-discounts {
  margin-bottom: 0;
  > .product-discounts-title {
    font-weight: normal;
    font-size: $font-size-sm;
  }
  > .table-product-discounts {
    thead tr th {
      width: 33%;
      padding: $small-space $medium-space;
      background: white;
      border: 0.3125rem $gray-light solid;
      text-align: center;
    }
    tbody tr {
      background: $gray-lighter;
      &:nth-of-type(even) {
        background: white;
      }
      td {
        padding: $small-space $medium-space;
        text-align: center;
        border: 0.3125rem $gray-light solid;
      }
    }
  }
}
.product-prices {
  font-weight: 400;
  font-size: 15px;
  color:$black-color;
  margin-bottom: 20px;
  div {
    display: inline-block;
    vertical-align: middle;
  }

  .tax-shipping-delivery-label {
    font-size: 0.8125rem;
    color: $gray;

    .delivery-information {
      padding: 0 0 0 2px;

      &::before {
        content: "-";
        padding: 0 2px 0 0;
      }
    }
  }
}
.product-discount {
  color: #878787;
  margin-bottom: 0;
  .regular-price {
    text-decoration: line-through;
    font-weight: 500;
    margin-right: $small-space;
    font-size: 18px;
  }
}
.product-line-info {
  margin-bottom: 8px;
  .value {
    color: #acaaa6;
  }
}
.has-discount {
  &.product-price, p {
    font-weight: 500;
    font-size: 24px;
    margin-bottom: 0;
  }
  .discount {
    background: #f1f1f1;
    color: #000000;
    padding: 7px 11px;
    font-size: 13px;
    line-height: 15px;
    font-weight: 400;
    margin: 0 8px;
    text-transform: uppercase;
    display: inline-block;
    vertical-align: middle;
  }
}
.product-unit-price {
  font-size: $font-size-xs;
  margin-bottom: 0;
}
.tabs {
  @include media-breakpoint-down(md) {/* If media is below 992 */
    margin-bottom:30px;
  }
  margin-bottom: 50px;
  .nav-tabs {
    border-bottom: none;
    margin-bottom: 30px;
    text-align: center;
    .nav-link {
      color: $brand-primary;
      padding-left:1px;
      padding-right:1px;
      padding: 8px 12px;
      border: 1px solid #e4e4e4;
      background: $light-color;
      letter-spacing: 1px;
      text-decoration: none;
      -webkit-transition: all 0.5s;
      -moz-transition: all 0.5s;
      -o-transition: all 0.5s;
      transition: all 0.5s;
      font-size: 14px;
      font-weight: 400;
      border-radius: 4px;
      position: relative;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        width: 0;
        height: 0;
        bottom: -9px;
        margin: 0 auto;
        border-style: solid;
        border-width: 8px 10px 0 10px;
        border-color: transparent transparent transparent transparent;
        -webkit-transition: all 0.5s;
        -moz-transition: all 0.5s;
        -o-transition: all 0.5s;
        transition: all 0.5s;
      }
      @include media-breakpoint-down(xs) {
        padding: 8px 4px;
        font-size: 13px;
      }
      &.active {
        border-color:$brand-secondary;
        background: $brand-secondary;
        color: $light-color;
        &:before {
          border-color: $brand-secondary transparent transparent transparent;
        }
      }
      &:hover {
        border-color:$brand-secondary;
        background: $brand-secondary;
        color: $light-color;
        &:before {
          border-color: $brand-secondary transparent transparent transparent;
        }
      }
      &:focus {
        color: $light-color;
      }
    }
    .nav-item {
      display: inline-block;
      vertical-align: middle;
      float: none;
      margin-bottom: -0.125rem;
      text-transform: uppercase;
      margin-left: 10px;
      @include media-breakpoint-down(sm) {/* If media is below 768 */
        margin-bottom: 15px;
        &:last-child{
          margin-bottom: 0;
          margin-left: 0;
        }
      }
      @include media-breakpoint-down(xs) {
        margin-left: 6px;
      }
      &:first-child {
        margin-left: 0;
      }
    }
  }
  #tab-content {
    padding: 15px;
    border: 1px solid #e4e4e4;
    text-align: center;
    .tab-pane {
      p{
        line-height: 28px;
        color: gray;
        margin-bottom: 0;
        letter-spacing: 0.8px;
      }
    }
  }    
}
.product-cover {
  margin-bottom: $medium-space;
  position: relative;
  #product-zoom {
    cursor: crosshair;
  }
  img {
    background: white;
  }
}
#product-modal {
  .modal-content {
    background: transparent;
    border: none;
    padding: 0;
    .modal-body {
      display: flex;
      margin-left: -30%;
      .product-cover-modal {
        background: white;
      }
      .image-caption {
        background: white;
        width: 800px;
        padding: 0.625rem 1.25rem;
        border-top: $gray-light 1px solid;
        p {
          margin-bottom: 0;
        }
      }
      .thumbnails {
        position: relative;
      }
      .mask {
        position: relative;
        overflow: hidden;
        max-height: 49.38rem;
        margin-top: 2.188rem;        
        z-index: 1;
        &.nomargin {
          margin-top: 0;
        }
      }
      .product-images {
        margin-left: $extra-large-space;
        img {
          width: 9.25rem;
          cursor: pointer;
          background: white;
          &:hover {
            border: $brand-primary 3px solid;
          }
        }
      }
      .arrows {
        height: 100%;
        width: 100%;
        text-align: center;
        position: absolute;
        top: 0;
        color: white;
        right: $large-space;
        z-index: 0;
        .arrow-up {
          position: absolute;
          top: -2rem;
          opacity: 0.2;
        }
        .arrow-down {
          position: absolute;
          bottom: -2rem;
        }
        i {
          font-size: 6.25rem;
          display: inline;
        }
        cursor: pointer;
      }
    }
  }
}
#blockcart-modal {
  color: $gray-darker;
  .modal-header {
    color: $brand-primary;
    .close {
      margin-top: -9px;
    font-size: 42px;
    padding: 0 13px;
    position: absolute;
    right: 5px;
    font-weight: 100;
    opacity: 1;
    }
  }
  .modal-body {
    background: $light-color;
    padding: 35px 25px;
    .divide-right span {
      display: inline-block;
      margin-bottom: 10px;
    }
  }
  .modal-dialog {
    max-width: 1140px;
    width: 100%;
  }
  .product-image {
    width: 100%;
  }
  .modal-title {
    line-height: 24px;
    font-size: 16px;
    font-weight: 500;
    text-align: left !important;
    i.material-icons {
      margin-right: 10px;
    }
  }
  .product-name, .cart-products-count {    
    color: #333;
    font-size: 16px;
    text-transform: capitalize;
    font-weight: 400;
    line-height: 20px;
    margin-bottom: 15px;
  }
  .cart-products-count {  

    border-bottom: 1px solid #e6e6e6;
  padding-bottom: 10px;
  margin-bottom: 15px;
  }
  .cart-content {
    padding-left: $extra-large-space;
    .cart-content-btn {
      display: inline-flex;
      button {
        margin-right: 0.3125rem;
      }
      .btn {
        white-space: inherit;
      }
      i{
        display: none;
      }
      .btn-secondary {
        background-color: $brand-secondary;
        border-color: $brand-secondary;
        color: $light-color;
      &:hover {
        background-color: $thired-color;
        border-color: $thired-color;
        color: $brand-primary;
      }
      }
    }
    strong{
      font-weight: 600;
    color: #000;
    }
  }
  .divide-right {
    border-right: $gray-light 1px solid;
    span, p{
      color: $icon-color;
      font-size: 14px;
      strong{
        font-weight: 600;
        color:#000;
      }
    }
    .product-price-modal {
      font-size: 18px;
      color: $brand-secondary;
    }
  }
}
.product-images {
  &.qv-carousel{
    margin-bottom: 50px;
  }
  > li.thumb-container {
    display: inline;
    > .thumb {
      cursor: pointer;
      margin-bottom: $small-space;
      .images-container & {
        margin-right: 0.8125rem;
      }
    }
  }
}
#main {
  .images-container {
    .js-qv-mask {
      white-space: nowrap;
      padding: 0 15px;
      &.scroll {
        width: 450px;
        margin: 0 auto;
        @include media-breakpoint-down(lg){
          width: 350px;
        }
        @include media-breakpoint-down(md){
          width: 230px;
        }
        @include media-breakpoint-down(sm) {
          width: 350px;
        }
        @include media-breakpoint-down(xs) {
          width: 230px;
        }
      }
      .owl-nav {
        position: relative;
        right: auto;
        top: -68px;
        .owl-prev {
            left: -20px;
            right: auto;
            position: absolute;
            top: 33%;
            margin: 0;
        }
        .owl-next {
            right: -20px;
            left: auto;
            position: absolute;
            top: 33%;
            margin: 0;
        }
      }
    }
  }
}
.scroll-box-arrows {
  display: none;
  &.scroll {
    display: block;
  }
  i {
    position: absolute;
    bottom: 5%;
    height: 100px;
    line-height: 100px;
    cursor: pointer;
  }
  .left {
    left: 0;
  }
  .right {
    right: 0;
  }
}
#product-availability {
  @include media-breakpoint-down(md) {/* If media is below 992 */
    margin-left: 0;
  }
  @include media-breakpoint-down(sm) {/* If media is below 768 */
    margin-left: 10px;
  }
  @include media-breakpoint-down(xs) {/* If media is below 544 */
    margin: 0;
    width: 100%;   
  }  
  display: inline-block;
  font-weight: 600;
  margin: 10px 0 0 15px;
  vertical-align: middle;
  .material-icons {
    line-height: inherit;
    line-height: 22px;
  }
  .product-available {
    color: $brand-success;
  }
  .product-unavailable {
    color: $brand-warning;
  }
  .product-last-items {
    color: $brand-warning;
  }
}
#product-details {
  .product-manufacturer {
    margin-bottom: 15px;
  }
  .label {
    font-weight: 500;
    margin-right: 10px;
    color: #444;
    margin-bottom: 12px;
  }
  span {
    color: #969696;
    line-height: 20px;
  }
}
.product-features {
  margin-top: $medium-space;
  margin-left: 0.3125rem;
  text-align: center;
  > dl.data-sheet {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    dd.value,
    dt.name {
      flex: 1 0 40%;
      font-weight: normal;
      background: #ebebeb;
      padding: $small-space;
      margin-right: $small-space;
      min-height: 2.5rem;
      word-break: normal;
      text-transform: capitalize;
      margin-bottom: .5rem;
      &:nth-of-type(even) {
        background: #f6f6f6;
      }
    }
  }
}
.product-variants {
  > .product-variants-item {
    margin-bottom: 20px;
    &:last-child{
      margin-bottom: 20px;
    }
    select {
      background-color: white;
      width: auto;
      padding-right: 1.875rem;
    }
    ul{
      margin-bottom: 0;
    }
    label{
      margin-bottom: 0;
    }
    ul li {
      margin-right: 2px;
      display: inline-block;
      vertical-align: middle;
    }
    .color {
      margin-left: 0;
      margin-top: 0;
    }
  }
}
.product-add-to-cart {
  display: block;
  overflow: hidden;
  margin-bottom: 15px;
}
.product-flags {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
}
li.product-flag {
    position: absolute;
    display: block;
    padding: 5px 7px;
    color: $gray-darker;
    text-transform: capitalize;
    font-size: 14px;
    font-weight: 400;
    z-index: 2;
 
    &.discount, &.on-sale{
        top: 10px;
        right: 10px;
    }
    &.new {
      top: 10px;
      left: 10px;
    }
    &.online-only {
        left: 10px;
        bottom: 10px;
        display: none;
        &::before{
            content:"\E30A";
            font-family: 'Material Icons';
            vertical-align: -2px;
            margin: 5px;
            font-size: 12px;
        }
    }
    &.pack {
        color: $brand-primary;
        right: 10px;
        top: 30px;
    }
}

.product-customization {
  @include box-shadow;
  margin: $extra-large-space 0;
  .product-customization-item {
    margin: $medium-space 0;
  }
  .product-message {
    background: $gray-light;
    border: none;
    width: 100%;
    height: 3.125rem;
    resize: none;
    padding: 0.625rem;
    &:focus {
      background-color: white;
    }
  }
  .file-input {
    width: 100%;
    opacity: 0;
    left: 0;
    z-index: 1;
    cursor: pointer;
    height: 2.625rem;
    overflow: hidden;
    position: absolute;
  }

  .customization-message{
    margin-top: 20px;
  }
  .custom-file {
    position: relative;
    background: $gray-light;
    width: 100%;
    height: 2.625rem;
    line-height: 2.625rem;
    text-indent: 0.625rem;
    display: block;
    color: $gray;
    margin-top: $medium-space;
    button {
      z-index: 0;
      position: absolute;
      right: 0;
      top: 0;
    }
  }
  small {
    color: $gray;
  }
}
.product-pack {
  margin-top: $extra-large-space;
  .pack-product-container {
    display: flex;
    justify-content: space-around;
    .pack-product-name {
      flex: 0 0 50%;
      font-size: 0.875rem;
      color: $gray;
    }
    .pack-product-quantity {
      border-left: $gray-light 2px solid;
      padding-left: $small-space;
    }
    .pack-product-name,
    .pack-product-price,
    .pack-product-quantity {
      @include display(flex);
      align-items: center;
    }
  }
}
.product-refresh {
  margin-top: $medium-space;
}
.product-additional-info {
  display: block;
  overflow: hidden;
}
.social-sharing {  
  margin-bottom: 20px;
  span {
    display: block;
    margin-bottom: 0.375rem;
    font-weight: 500;
  }
  ul {
    margin-bottom: 0;
  }
  li{
      height: 35px;
      width: 60px;
      display: inline-block;
      background-color: #FFF;
      cursor: pointer;
      margin-right: 4px;
      -webkit-transition: all 0.2s ease-in;
      -moz-transition: all 0.2s ease-in;
      -o-transition: all 0.2s ease-in;
      transition: all 0.2s ease-in;
      a {
        display: block;
        width: 100%;
        height: 100%;
        white-space: nowrap;
        text-align: center;
        line-height: 35px;
        text-indent: unset;
        overflow: hidden;
        &:hover{
          color: transparent;
        }
      }
    }
}

.wishlist_container {
  margin-bottom: 10px;
}

.paymentlogo-container { 
  @include media-breakpoint-down(xs) {/* If media is below 544 */
    display: none;
  }
  padding: 0;
  margin-bottom: 20px;
  width: 100%;
  span {
    display: block;
    margin-bottom: 0.375rem;
    font-weight: 500;
  }
  img {
    background: #f4f4f4;
    margin: 1px 4px 1px 0;
    padding: 5px;
  }   
}

.wishlist_login{
  clear: both;
}
#product-list-top {
  padding: 14px 15px;
  background-color: #f5f5f5;
  margin-bottom: 15px;
  .products-selection {
    .title {
      color: $gray-dark;
    }
  }
}
#js-product-list-top {
  .layout-options {
    display: block;
    float: left;
    margin-right: 10px;
    display: inline-block;
    vertical-align: middle;
    > span {
      width: 22px;
      height: 22px;
      display: inline-block;
      cursor: pointer;
      margin: 1px;
      -webkit-transition: all 0.25s;
      -moz-transition: all 0.25s;
      -o-transition: all 0.25s;
      transition: all 0.25s;
    }
    #grid-view {
      background:url('../img/layout_sprite.png') no-repeat 0px 0px;
      &:hover {
        background-position:0px -33px;
      }
      &.checked {
        background-position:0px -33px
      }
    }
    #list-view {
      background:url('../img/layout_sprite.png') no-repeat -37px 1px;
      &:hover {
        background-position:-37px -32px;
      }
      &.checked {
        background-position:-37px -32px;
      }
    }
  }
}
#blockcart-modal .cart-content {
  .btn {
    margin-bottom: $small-space;
  }
}
@include media-breakpoint-down(md) {
  .product-cover {
    img {
      width: 100%;
    }
  }
  #product-modal .modal-content .modal-body {
    flex-direction: column;
    margin-left: 0;
    img.product-cover-modal {
      width: 100%;
    }
    .arrows {
      display: none;
    }
  }
  #product-modal .modal-content .modal-body .image-caption {
    width: 100%;
  }
  #blockcart-modal {
    .modal-dialog {
      width: calc(100% - 20px);
    }
    .modal-body {
      padding: 1.875rem;
    }
  }
}

@include media-breakpoint-down(sm) {
  #blockcart-modal {
    .divide-right {
      border-right: none;
    }
    .modal-body {
      padding: 1rem;
    }
  }
}



