.product-block-information{
  .product-title{
    font-size: 1.125rem;
    padding-bottom: 0;
    border-bottom: medium none;
    margin-bottom: 0.5rem; 
  }
}
}


.product-accessories-block{
  display: block;
  width: 100%;
  overflow: hidden;
  margin-bottom: 30px;
}

#product{
  #ishispecialproducts{
    padding:  0 15px;
    display: block;
    width: 100%;
    overflow: hidden;
  }
}


