.quickview {
  .modal-dialog {
    width: calc(100% - 30px);
    max-width: 64rem;
    margin: 10px auto;
  }
  .modal-content {
    min-height: 28.13rem;
  }
  #product-availability {
    @include media-breakpoint-down(md) {/* If media is below 992 */
      width: 100%;
    }
    @include media-breakpoint-down(sm) {/* If media is below 768 */
      width: auto;
    }
    @include media-breakpoint-down(xs) {/* If media is below 544 */
      width: 100%;
    }
  }
  .modal-header {
    border: medium none;
    padding: 0;
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: 1;
    .close {
      margin-top: -7px;
      color: #333;
      opacity: 1;
      font-size: 35px;
      margin-right: 10px;
    }
  }
  .modal-body {
    min-height: rem(380px);
    padding:40px 15px;
    .product-title{
      border-bottom: 1px solid #e5e5e5;
      font-size: 20px;
      font-weight: 400;
      margin: 0 0 15px;
      padding: 0 0 10px;
    }
    .product-comments{
      margin-bottom: 15px;
      text-align: left;
      .star_content{
        display: block;
        padding: 0;
        &:after{
          display: none;
        }
      }
      .comment_advice{
        display: none;
      }
    }
    span{
      font-size: 14px;
      font-weight: 600;
      margin-bottom: 8px;
      &.discount {
          margin-bottom: 2px;
      }
    }
    .regular-price {
      font-weight: 500;
      font-size: 18px;
    }
    .current-price span {
      &:first-child {
      font-weight: 500;
      font-size: 24px;
      }
    }
    span.color {
      margin:0;
    }
    .qv-carousel {
      margin-bottom: 0;
    }

  }
  .modal-footer {
    border-top: 1px solid rgba($gray,.3);
    text-align: center;
    .buttons_bottom_block {
      display: none;
    }
    .social-sharing {
      display: block;
    }
  }
  .layer {
    display: none;
  }
  .product-cover img {
    width: 95%;
  }
  .images-container {
    display: block;
    @include media-breakpoint-down(xs) {/* If media is below 544 */ 
        max-width: 300px;
        margin:10px auto;
    }
    .product-images > li.thumb-container > .thumb {
      width: 4.938rem;
      margin-bottom: 0.8125rem;
      background: white;
    }
  }
  .mask {
    padding: 0 15px;
    @include media-breakpoint-down(md) {
       width: 27%;
    }
    @include media-breakpoint-down(xs) {/* If media is below 544 */ 
        display: none
    }
    .owl-nav {
      position: relative;
      right: auto;
      top: -55px;
      .owl-prev {
          left: -15px;
          right: auto;
          position: absolute;
          top: 31%;
          margin: 0;
      }
      .owl-next {
          right: -15px;
          left: auto;
          position: absolute;
          top: 31%;
          margin: 0;
      }
    }
  }
  .arrows {
    position: absolute;
    top: 0;
    height: 480px;
    right: 55px;
    z-index: 0;
    @include media-breakpoint-down(md) {/* If media is below 992 */ 
      right: 40px;
    }
    @include media-breakpoint-down(sm) {/* If media is below 768 */
      right: 90px;
    }
    @include media-breakpoint-down(xs) {/* If media is below 544 */ 
        display: none
    }
    .arrow-up {
      margin-top: -3.125rem;
      cursor: pointer;
      opacity: 0.2;
    }
    .arrow-down {
      position: absolute;
      bottom: -1.875rem;
      left: 0;
      cursor: pointer;
    }
  }
  .social-sharing {
    display: none;
    span {
      display: none;
    }
    margin-bottom: 0;
    text-align: left;
  }
  .paymentlogo-container {
    display: none;
  }
}




