#ishiservices{
	text-align: center;
	margin-bottom: 40px;	
	@include media-breakpoint-down(md) {/* If media is below 992 */
		margin-bottom: 20px;
	}
	.home-title{
		display: none;
	}
	#ishiservices-content{
		border : 1px solid #eee;
	}
    .services{
		cursor: pointer;
		padding: 25px 0 30px 0;
		border-right: 1px solid #eee;
		@include media-breakpoint-down(md) {/* If media is below 992 */
			margin-bottom: 30px;
			&:last-child{
				margin-bottom:0;
			}
		}
		&:last-child{
			border-right: none;
		}
		@include media-breakpoint-down(md) {/* If media is below 992 */
			&:nth-child(2){
				border-right: none;
			}
		}
		@include media-breakpoint-down(xs) {/* If media is below 544 */
			padding: 20px 0 20px 0;
			margin-bottom: 0;
		}
		a{
			display: inline-block;
			.service-content{
				@include media-breakpoint-down(sm) {/* If media is below 768 */
					padding: 5px 0;
				}
			}
		}
		.service-img{
			height: 75px;
			width: 75px;
			background-position: 0px -90px;
			background-repeat: no-repeat;
			margin: 0 auto;
			margin-bottom: 10px;
			-webkit-transition: all 0.5s;
	          -moz-transition: all 0.5s;
	          -o-transition: all 0.5s;
	          transition: all 0.5s;
		}
		.service-block{
			display: block;
			.service-title{
				@include media-breakpoint-down(md) {/* If media is below 992 */			
					margin-top: 4px;
				}	
				font-size: 18px;
			    font-weight: 500;
			    margin: 10px 0;
			    text-transform: uppercase;
			    color: $brand-primary;
			    font-family: $secondary-font;
			    transition: all 300ms ease;
				-webkit-transition: all 0.5s;
		          -moz-transition: all 0.5s;
		          -o-transition: all 0.5s;
		          transition: all 0.5s;
			}
			.service-desc{
				color: #333;
				font-weight: 300;
				-webkit-transition: all 0.5s;
		          -moz-transition: all 0.5s;
		          -o-transition: all 0.5s;
		          transition: all 0.5s;
				@include media-breakpoint-down(xs) {/* If media is below 544 */
					font-size: 12px;
				}
			}
		}
		&:hover{
			.service-img{
				background-position: 0px -15px;			
			}
			.service-block{
				.service-title{
					color: $brand-secondary;
				}
				.service-desc{
					letter-spacing: 1.3px;
				}
			}
		}
	}
}


